import { useEffect, useState } from 'react';
import { useAuth } from '../context/AuthContext';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { Input } from '../components/ui/input';
import { Textarea } from '../components/ui/textarea';
import { Label } from '../components/ui/label';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../components/ui/select';
import { Checkbox } from '../components/ui/checkbox';
import { 
    Package, Users, ShoppingBag, Settings, Plus, Edit, Trash2, 
    Search, ArrowLeft, DollarSign, TrendingUp, Zap, Gift
} from 'lucide-react';
import { toast } from 'sonner';
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '../components/ui/dialog';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

const statusConfig = {
    pending: { label: 'Pendiente', color: 'status-pending' },
    processing: { label: 'En Proceso', color: 'status-processing' },
    shipped: { label: 'Enviado', color: 'status-shipped' },
    completed: { label: 'Completado', color: 'status-completed' },
    cancelled: { label: 'Cancelado', color: 'status-cancelled' }
};

export default function AdminDashboard() {
    const { user, token, isAdmin, loading: authLoading } = useAuth();
    const navigate = useNavigate();
    
    // State management
    const [orders, setOrders] = useState([]);
    const [products, setProducts] = useState([]);
    const [users, setUsers] = useState([]);
    const [services, setServices] = useState([]);
    const [promotions, setPromotions] = useState([]);
    const [categories, setCategories] = useState([]);
    const [stats, setStats] = useState({});
    const [loading, setLoading] = useState(true);
    const [statusFilter, setStatusFilter] = useState('all');
    
    // Product form
    const [productForm, setProductForm] = useState({
        name: '', description: '', price: '', category: '', stock: '', image_url: ''
    });
    const [editingProduct, setEditingProduct] = useState(null);
    const [productDialogOpen, setProductDialogOpen] = useState(false);
    
    // Service form
    const [serviceForm, setServiceForm] = useState({
        name: '', description: '', price_per_hour: '', image_url: '', features: ''
    });
    const [editingService, setEditingService] = useState(null);
    const [serviceDialogOpen, setServiceDialogOpen] = useState(false);
    
    // Promotion form
    const [promotionForm, setPromotionForm] = useState({
        name: '', description: '', subtitle: '', image_url: '', discount_percentage: '', discount_fixed: '', 
        applicable_to: 'all', start_date: '', end_date: '', is_active: true, frequency: 'once_per_day'
    });
    const [editingPromotion, setEditingPromotion] = useState(null);
    const [promotionDialogOpen, setPromotionDialogOpen] = useState(false);

    // Category form
    const [categoryForm, setCategoryForm] = useState({
        name: '', description: '', image_url: ''
    });
    const [editingCategory, setEditingCategory] = useState(null);
    const [categoryDialogOpen, setCategoryDialogOpen] = useState(false);
    
    // About Us form
    const [aboutForm, setAboutForm] = useState({
        title: '', description: '', images: []
    });
    const [aboutDialogOpen, setAboutDialogOpen] = useState(false);
    
    // User form
    const [userForm, setUserForm] = useState({
        name: '', email: '', password: '', is_admin: false
    });
    const [userDialogOpen, setUserDialogOpen] = useState(false);
    
    // Image upload state
    const [uploadingImage, setUploadingImage] = useState(false);
    const [imagePreview, setImagePreview] = useState({});

    useEffect(() => {
        if (authLoading) return;
        
        if (!user || !isAdmin) {
            navigate('/login');
            return;
        }
        fetchData();
    }, [isAdmin, user, authLoading, navigate]);

    const fetchData = async () => {
        try {
            const [ordersRes, productsRes, usersRes, servicesRes, promotionsRes, categoriesRes, statsRes, aboutRes] = await Promise.all([
                axios.get(`${API}/admin/orders`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${API}/products`),
                axios.get(`${API}/admin/users`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${API}/admin/services`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${API}/admin/promotions`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${API}/admin/categories`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${API}/admin/stats`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${API}/about`, { headers: { Authorization: `Bearer ${token}` } }).catch(() => ({ data: {} }))
            ]);
            setOrders(ordersRes.data);
            setProducts(productsRes.data);
            setUsers(usersRes.data);
            setServices(servicesRes.data);
            setPromotions(promotionsRes.data);
            setCategories(categoriesRes.data);
            // Set default category for product form
            if (categoriesRes.data.length > 0 && !productForm.category) {
                setProductForm(prev => ({ ...prev, category: categoriesRes.data[0].name }));
            }
            setStats(statsRes.data);
            // Load about data
            if (aboutRes.data && aboutRes.data.title) {
                setAboutForm({
                    title: aboutRes.data.title,
                    description: aboutRes.data.description,
                    images: aboutRes.data.images || []
                });
            }
            // Scroll al top
            window.scrollTo(0, 0);
        } catch (err) {
            console.error('Error:', err);
            toast.error('Error al cargar datos');
        } finally {
            setLoading(false);
        }
    };

    // Order Management
    const updateOrderStatus = async (orderId, status) => {
        try {
            await axios.put(`${API}/admin/orders/${orderId}/status`, { status }, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setOrders(prev => prev.map(o => o.id === orderId ? { ...o, status } : o));
            toast.success('Estado actualizado');
        } catch (err) {
            toast.error('Error al actualizar estado');
        }
    };

    // Image Upload Management
    const handleImageUpload = async (file, formType) => {
        if (!file) return;
        
        setUploadingImage(true);
        const formData = new FormData();
        formData.append('file', file);
        
        try {
            const response = await axios.post(`${API}/admin/upload-image`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                    'Authorization': `Bearer ${token}`
                }
            });
            
            // Construir URL completa para la imagen
            const imageUrl = `${process.env.REACT_APP_BACKEND_URL}${response.data.url}`;
            
            // Actualizar el formulario correspondiente
            if (formType === 'product') {
                setProductForm({...productForm, image_url: imageUrl});
            } else if (formType === 'service') {
                setServiceForm({...serviceForm, image_url: imageUrl});
            } else if (formType === 'category') {
                setCategoryForm({...categoryForm, image_url: imageUrl});
            } else if (formType === 'promotion') {
                setPromotionForm({...promotionForm, image_url: imageUrl});
            } else if (formType === 'about') {
                // Para about, agregar la imagen a la lista
                setAboutForm(prev => ({
                    ...prev,
                    images: [...(prev.images || []), imageUrl]
                }));
            }
            
            toast.success('Imagen cargada exitosamente');
        } catch (err) {
            toast.error('Error al cargar la imagen');
            console.error(err);
        } finally {
            setUploadingImage(false);
        }
    };

    const handleFileSelect = (e, formType) => {
        const file = e.target.files[0];
        if (file) {
            // Mostrar preview
            const reader = new FileReader();
            reader.onload = (event) => {
                setImagePreview({...imagePreview, [formType]: event.target.result});
            };
            reader.readAsDataURL(file);
            
            // Cargar imagen
            handleImageUpload(file, formType);
        }
    };

    // Product Management
    const handleProductSubmit = async (e) => {
        e.preventDefault();
        
        // Validar que la categora no est vaca
        if (!productForm.category) {
            toast.error('Debe seleccionar una categora');
            return;
        }
        
        try {
            const data = {
                ...productForm,
                price: parseFloat(productForm.price),
                stock: parseInt(productForm.stock)
            };
            
            if (editingProduct) {
                await axios.put(`${API}/products/${editingProduct.id}`, data, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Producto actualizado');
            } else {
                await axios.post(`${API}/products`, data, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Producto creado');
            }
            
            setProductDialogOpen(false);
            setEditingProduct(null);
            setProductForm({ name: '', description: '', price: '', category: categories.length > 0 ? categories[0].name : '', stock: '', image_url: '' });
            setImagePreview({});
            fetchData();
        } catch (err) {
            console.error('Error:', err.response?.data);
            toast.error(err.response?.data?.detail || 'Error al guardar producto');
        }
    };

    const deleteProduct = async (productId) => {
        if (!window.confirm('Eliminar este producto?')) return;
        try {
            await axios.delete(`${API}/products/${productId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setProducts(prev => prev.filter(p => p.id !== productId));
            toast.success('Producto eliminado');
        } catch (err) {
            toast.error('Error al eliminar producto');
        }
    };

    const editProduct = (product) => {
        setEditingProduct(product);
        setProductForm({
            name: product.name,
            description: product.description,
            price: product.price.toString(),
            category: product.category,
            stock: product.stock.toString(),
            image_url: product.image_url || ''
        });
        setProductDialogOpen(true);
    };

    // Service Management
    const handleServiceSubmit = async (e) => {
        e.preventDefault();
        try {
            const data = {
                ...serviceForm,
                price_per_hour: parseFloat(serviceForm.price_per_hour),
                features: serviceForm.features.split(',').map(f => f.trim())
            };
            
            if (editingService) {
                await axios.put(`${API}/admin/services/${editingService.id}`, data, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Servicio actualizado');
            } else {
                await axios.post(`${API}/admin/services`, data, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Servicio creado');
            }
            
            setServiceDialogOpen(false);
            setEditingService(null);
            setServiceForm({ name: '', description: '', price_per_hour: '', image_url: '', features: '' });
            fetchData();
        } catch (err) {
            toast.error('Error al guardar servicio');
        }
    };

    const deleteService = async (serviceId) => {
        if (!window.confirm('Eliminar este servicio?')) return;
        try {
            await axios.delete(`${API}/admin/services/${serviceId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setServices(prev => prev.filter(s => s.id !== serviceId));
            toast.success('Servicio eliminado');
        } catch (err) {
            toast.error('Error al eliminar servicio');
        }
    };

    const editService = (service) => {
        setEditingService(service);
        setServiceForm({
            name: service.name,
            description: service.description,
            price_per_hour: service.price_per_hour.toString(),
            image_url: service.image_url || '',
            features: (service.features || []).join(', ')
        });
        setServiceDialogOpen(true);
    };

    // Promotion Management
    const handlePromotionSubmit = async (e) => {
        e.preventDefault();
        try {
            const data = {
                ...promotionForm,
                discount_percentage: parseFloat(promotionForm.discount_percentage || 0),
                discount_fixed: promotionForm.discount_fixed ? parseFloat(promotionForm.discount_fixed) : null
            };
            
            if (editingPromotion) {
                await axios.put(`${API}/admin/promotions/${editingPromotion.id}`, data, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Promocin actualizada');
            } else {
                await axios.post(`${API}/admin/promotions`, data, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Promocin creada');
            }
            
            setPromotionDialogOpen(false);
            setEditingPromotion(null);
            setPromotionForm({ 
                name: '', description: '', subtitle: '', image_url: '', discount_percentage: '', discount_fixed: '', 
                applicable_to: 'all', start_date: '', end_date: '', is_active: true, frequency: 'once_per_day'
            });
            fetchData();
        } catch (err) {
            toast.error('Error al guardar promocin');
        }
    };

    const deletePromotion = async (promotionId) => {
        if (!window.confirm('Eliminar esta promocin?')) return;
        try {
            await axios.delete(`${API}/admin/promotions/${promotionId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setPromotions(prev => prev.filter(p => p.id !== promotionId));
            toast.success('Promocin eliminada');
        } catch (err) {
            toast.error('Error al eliminar promocin');
        }
    };

    const editPromotion = (promo) => {
        setEditingPromotion(promo);
        setPromotionForm({
            name: promo.name,
            description: promo.description,
            subtitle: promo.subtitle || '',
            image_url: promo.image_url || '',
            discount_percentage: promo.discount_percentage.toString(),
            discount_fixed: promo.discount_fixed ? promo.discount_fixed.toString() : '',
            applicable_to: promo.applicable_to,
            start_date: promo.start_date,
            end_date: promo.end_date,
            is_active: promo.is_active,
            frequency: promo.frequency || 'once_per_day'
        });
        setPromotionDialogOpen(true);
    };

    const deleteUser = async (userId) => {
        if (!window.confirm('Eliminar este usuario? Esta accin no se puede deshacer.')) return;
        try {
            await axios.delete(`${API}/admin/users/${userId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setUsers(prev => prev.filter(u => u.id !== userId));
            toast.success('Usuario eliminado');
        } catch (err) {
            toast.error('Error al eliminar usuario');
        }
    };

    // Category functions
    const handleCategorySubmit = async (e) => {
        e.preventDefault();
        try {
            if (editingCategory) {
                await axios.put(`${API}/admin/categories/${editingCategory.id}`, categoryForm, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Categora actualizada');
            } else {
                await axios.post(`${API}/admin/categories`, categoryForm, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                toast.success('Categora creada');
            }
            setCategoryDialogOpen(false);
            setEditingCategory(null);
            setCategoryForm({ name: '', description: '', image_url: '' });
            setImagePreview({});
            fetchData();
        } catch (err) {
            toast.error(err.response?.data?.detail || 'Error al guardar categora');
        }
    };

    const editCategory = (category) => {
        setEditingCategory(category);
        setCategoryForm({
            name: category.name,
            description: category.description || '',
            image_url: category.image_url || ''
        });
        setCategoryDialogOpen(true);
    };

    const deleteCategory = async (categoryId) => {
        if (!window.confirm('Eliminar esta categora? Esta accin no se puede deshacer.')) return;
        try {
            await axios.delete(`${API}/admin/categories/${categoryId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setCategories(prev => prev.filter(c => c.id !== categoryId));
            toast.success('Categora eliminada');
        } catch (err) {
            toast.error('Error al eliminar categora');
        }
    };

    const handleAboutSubmit = async (e) => {
        e.preventDefault();
        try {
            await axios.post(`${API}/admin/about`, aboutForm, {
                headers: { Authorization: `Bearer ${token}` }
            });
            toast.success('Informacin guardada correctamente');
            setAboutDialogOpen(false);
            fetchData();
        } catch (err) {
            toast.error(err.response?.data?.detail || 'Error al guardar informacin');
        }
    };

    const removeImageFromAbout = (index) => {
        setAboutForm(prev => ({
            ...prev,
            images: prev.images.filter((_, i) => i !== index)
        }));
    };

    const handleAddUser = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post(`${API}/auth/register`, {
                name: userForm.name,
                email: userForm.email,
                password: userForm.password,
                is_admin: userForm.is_admin
            });
            
            setUsers(prev => [...prev, response.data]);
            setUserForm({ name: '', email: '', password: '', is_admin: false });
            setUserDialogOpen(false);
            toast.success('Usuario agregado exitosamente');
        } catch (err) {
            toast.error(err.response?.data?.detail || 'Error al agregar usuario');
        }
    };

    const handleToggleAdminRole = async (userId, newStatus) => {
        try {
            await axios.patch(`${API}/admin/users/${userId}/role`, 
                { is_admin: newStatus },
                { headers: { Authorization: `Bearer ${token}` } }
            );
            
            setUsers(prev => prev.map(u => 
                u.id === userId ? { ...u, is_admin: newStatus } : u
            ));
            toast.success(`Rol actualizado: ${newStatus ? 'Administrador' : 'Usuario'}`);
        } catch (err) {
            toast.error('Error al actualizar rol');
        }
    };

    const filteredOrders = statusFilter === 'all' 
        ? orders 
        : orders.filter(o => o.status === statusFilter);

    if (loading || authLoading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-[#030303]">
                <div className="loading-spinner" />
            </div>
        );
    }

    if (!isAdmin) {
        return (
            <div className="min-h-screen bg-[#030303] pt-24 flex items-center justify-center">
                <div className="text-center">
                    <Settings className="w-24 h-24 text-white/20 mx-auto mb-6" />
                    <h2 className="text-2xl font-bold text-white mb-4">Acceso Restringido</h2>
                    <p className="text-white/50 mb-8">Necesitas ser administrador para acceder</p>
                    <Link to="/login">
                        <Button className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90">
                            Iniciar Sesin
                        </Button>
                    </Link>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            <div className="max-w-7xl mx-auto px-4 py-8">
                <div className="flex items-center gap-4 mb-8">
                    <Link to="/" className="text-white/50 hover:text-white">
                        <ArrowLeft className="w-6 h-6" />
                    </Link>
                    <h1 
                        className="text-3xl font-bold text-white"
                        style={{ fontFamily: 'Orbitron' }}
                    >
                        <span className="text-[#FF00FF]">COMMAND</span> CENTER
                    </h1>
                </div>

                {/* Stats */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
                    <Card className="bg-black/50 border-white/10">
                        <CardContent className="p-4 flex items-center gap-4">
                            <div className="w-12 h-12 rounded-lg bg-[#00FFFF]/20 flex items-center justify-center">
                                <Package className="w-6 h-6 text-[#00FFFF]" />
                            </div>
                            <div>
                                <p className="text-white/50 text-xs">Pedidos Totales</p>
                                <p className="text-white text-2xl font-bold">{stats.total_orders || 0}</p>
                            </div>
                        </CardContent>
                    </Card>
                    <Card className="bg-black/50 border-white/10">
                        <CardContent className="p-4 flex items-center gap-4">
                            <div className="w-12 h-12 rounded-lg bg-yellow-500/20 flex items-center justify-center">
                                <TrendingUp className="w-6 h-6 text-yellow-500" />
                            </div>
                            <div>
                                <p className="text-white/50 text-xs">Ingresos Totales</p>
                                <p className="text-white text-2xl font-bold">${(stats.total_revenue || 0).toFixed(0)}</p>
                            </div>
                        </CardContent>
                    </Card>
                    <Card className="bg-black/50 border-white/10">
                        <CardContent className="p-4 flex items-center gap-4">
                            <div className="w-12 h-12 rounded-lg bg-[#FF00FF]/20 flex items-center justify-center">
                                <Users className="w-6 h-6 text-[#FF00FF]" />
                            </div>
                            <div>
                                <p className="text-white/50 text-xs">Total de Usuarios</p>
                                <p className="text-white text-2xl font-bold">{stats.total_users || 0}</p>
                            </div>
                        </CardContent>
                    </Card>
                    <Card className="bg-black/50 border-white/10">
                        <CardContent className="p-4 flex items-center gap-4">
                            <div className="w-12 h-12 rounded-lg bg-green-500/20 flex items-center justify-center">
                                <ShoppingBag className="w-6 h-6 text-green-500" />
                            </div>
                            <div>
                                <p className="text-white/50 text-xs">Total de Productos</p>
                                <p className="text-white text-2xl font-bold">{stats.total_products || 0}</p>
                            </div>
                        </CardContent>
                    </Card>
                </div>

                {/* Tabs */}
                <Tabs defaultValue="orders" className="w-full">
                    <TabsList className="bg-black/50 border border-white/10 mb-6 grid w-full grid-cols-7">
                        <TabsTrigger value="orders" className="data-[state=active]:bg-[#00FFFF] data-[state=active]:text-black">
                            <Package className="w-4 h-4 mr-2" />
                            Pedidos
                        </TabsTrigger>
                        <TabsTrigger value="products" className="data-[state=active]:bg-[#FF00FF] data-[state=active]:text-white">
                            <ShoppingBag className="w-4 h-4 mr-2" />
                            Productos
                        </TabsTrigger>
                        <TabsTrigger value="services" className="data-[state=active]:bg-[#00FFFF] data-[state=active]:text-black">
                            <Zap className="w-4 h-4 mr-2" />
                            Servicios
                        </TabsTrigger>
                        <TabsTrigger value="categories" className="data-[state=active]:bg-[#FF00FF] data-[state=active]:text-white">
                            <ShoppingBag className="w-4 h-4 mr-2" />
                            Categoras
                        </TabsTrigger>
                        <TabsTrigger value="promotions" className="data-[state=active]:bg-[#FF00FF] data-[state=active]:text-white">
                            <Gift className="w-4 h-4 mr-2" />
                            Promociones
                        </TabsTrigger>
                        <TabsTrigger value="about" className="data-[state=active]:bg-[#00FFFF] data-[state=active]:text-black">
                            <Users className="w-4 h-4 mr-2" />
                            Sobre Nosotros
                        </TabsTrigger>
                        <TabsTrigger value="users" className="data-[state=active]:bg-purple-500 data-[state=active]:text-white">
                            <Users className="w-4 h-4 mr-2" />
                            Usuarios
                        </TabsTrigger>
                    </TabsList>

                    {/* Orders Tab */}
                    <TabsContent value="orders">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Pedidos</CardTitle>
                                    <Select value={statusFilter} onValueChange={setStatusFilter}>
                                        <SelectTrigger className="w-[180px] bg-black/50 border-white/20">
                                            <SelectValue placeholder="Filtrar por estado" />
                                        </SelectTrigger>
                                        <SelectContent className="bg-black border-white/20">
                                            <SelectItem value="all">Todos</SelectItem>
                                            {Object.entries(statusConfig).map(([key, val]) => (
                                                <SelectItem key={key} value={key}>{val.label}</SelectItem>
                                            ))}
                                        </SelectContent>
                                    </Select>
                                </div>
                            </CardHeader>
                            <CardContent className="p-0">
                                <div className="overflow-x-auto">
                                    <table className="w-full">
                                        <thead>
                                            <tr className="border-b border-white/10">
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Pedido</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Cliente</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Total</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Estado</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Fecha</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {filteredOrders.map((order) => (
                                                <tr key={order.id} className="border-b border-white/5 hover:bg-white/5">
                                                    <td className="p-4">
                                                        <p className="text-white font-mono text-sm">#{order.id.slice(0, 8)}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white">{order.user_name}</p>
                                                        <p className="text-white/40 text-xs">{order.user_email}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-[#00FFFF] font-bold">${order.total.toFixed(2)}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <span className={`px-2 py-1 rounded text-xs ${statusConfig[order.status]?.color || ''}`}>
                                                            {statusConfig[order.status]?.label}
                                                        </span>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white/60 text-sm">
                                                            {new Date(order.created_at).toLocaleDateString()}
                                                        </p>
                                                    </td>
                                                    <td className="p-4">
                                                        <Select 
                                                            value={order.status} 
                                                            onValueChange={(val) => updateOrderStatus(order.id, val)}
                                                        >
                                                            <SelectTrigger className="w-[140px] h-8 bg-black/50 border-white/20 text-xs">
                                                                <SelectValue />
                                                            </SelectTrigger>
                                                            <SelectContent className="bg-black border-white/20">
                                                                {Object.entries(statusConfig).map(([key, val]) => (
                                                                    <SelectItem key={key} value={key}>{val.label}</SelectItem>
                                                                ))}
                                                            </SelectContent>
                                                        </Select>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>

                    {/* Products Tab */}
                    <TabsContent value="products">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Productos ({products.length})</CardTitle>
                                    <Dialog open={productDialogOpen} onOpenChange={setProductDialogOpen}>
                                        <DialogTrigger asChild>
                                            <Button 
                                                className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90"
                                                onClick={() => {
                                                    setEditingProduct(null);
                                                    setProductForm({ name: '', description: '', price: '', category: categories.length > 0 ? categories[0].name : '', stock: '', image_url: '' });
                                                    setImagePreview({});
                                                }}
                                            >
                                                <Plus className="w-4 h-4 mr-2" />
                                                Nuevo Producto
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="bg-black/95 border-white/10 max-w-sm md:max-w-md w-[95vw] max-h-[90vh] overflow-y-auto">
                                            <DialogHeader>
                                                <DialogTitle className="text-white text-lg md:text-xl">
                                                    {editingProduct ? 'Editar Producto' : 'Nuevo Producto'}
                                                </DialogTitle>
                                            </DialogHeader>
                                            <form onSubmit={handleProductSubmit} className="space-y-3 md:space-y-4">
                                                <div>
                                                    <Label className="text-white/70 text-sm">Nombre</Label>
                                                    <Input
                                                        value={productForm.name}
                                                        onChange={(e) => setProductForm({...productForm, name: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Descripcin</Label>
                                                    <Textarea
                                                        value={productForm.description}
                                                        onChange={(e) => setProductForm({...productForm, description: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div className="grid grid-cols-2 gap-2 md:gap-3">
                                                    <div>
                                                        <Label className="text-white/70 text-xs md:text-sm">Precio ($)</Label>
                                                        <Input
                                                            type="number"
                                                            step="0.01"
                                                            value={productForm.price}
                                                            onChange={(e) => setProductForm({...productForm, price: e.target.value})}
                                                            className="cyber-input text-sm"
                                                            required
                                                        />
                                                    </div>
                                                    <div>
                                                        <Label className="text-white/70 text-xs md:text-sm">Stock</Label>
                                                        <Input
                                                            type="number"
                                                            value={productForm.stock}
                                                            onChange={(e) => setProductForm({...productForm, stock: e.target.value})}
                                                            className="cyber-input text-sm"
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Categora</Label>
                                                    <Select value={productForm.category} onValueChange={(val) => setProductForm({...productForm, category: val})}>
                                                        <SelectTrigger className="cyber-input text-sm">
                                                            <SelectValue placeholder="Seleccionar categora" />
                                                        </SelectTrigger>
                                                        <SelectContent className="bg-black border-white/20">
                                                            {categories.length > 0 ? (
                                                                categories.map(cat => (
                                                                    <SelectItem key={cat.id} value={cat.name}>{cat.name}</SelectItem>
                                                                ))
                                                            ) : (
                                                                <SelectItem value="" disabled>No hay categoras disponibles</SelectItem>
                                                            )}
                                                        </SelectContent>
                                                    </Select>
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Imagen del Producto</Label>
                                                    <div className="flex flex-col gap-2 md:gap-3">
                                                        <div className="flex flex-col sm:flex-row gap-2">
                                                            <Input
                                                                type="file"
                                                                accept="image/*"
                                                                onChange={(e) => handleFileSelect(e, 'product')}
                                                                className="cyber-input flex-1 text-xs"
                                                                disabled={uploadingImage}
                                                            />
                                                            {uploadingImage && <span className="text-[#00FFFF] text-xs self-center">Cargando...</span>}
                                                        </div>
                                                        {(imagePreview.product || productForm.image_url) && (
                                                            <img 
                                                                src={imagePreview.product || productForm.image_url} 
                                                                alt="Preview" 
                                                                className="w-full h-24 md:h-32 object-cover rounded border border-white/20"
                                                            />
                                                        )}
                                                        <div className="text-white/50 text-xs">O pegue una URL:</div>
                                                        <Input
                                                            value={productForm.image_url}
                                                            onChange={(e) => setProductForm({...productForm, image_url: e.target.value})}
                                                            className="cyber-input text-sm"
                                                            placeholder="https://..."
                                                        />
                                                    </div>
                                                </div>
                                                <Button type="submit" className="w-full bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-sm">
                                                    {editingProduct ? 'Actualizar' : 'Crear'} Producto
                                                </Button>
                                            </form>
                                        </DialogContent>
                                    </Dialog>
                                </div>
                            </CardHeader>
                            <CardContent className="p-0">
                                <div className="overflow-x-auto">
                                    <table className="w-full">
                                        <thead>
                                            <tr className="border-b border-white/10">
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Producto</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Categora</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Precio</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Stock</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {products.map((product) => (
                                                <tr key={product.id} className="border-b border-white/5 hover:bg-white/5">
                                                    <td className="p-4">
                                                        <div className="flex items-center gap-3">
                                                            <img 
                                                                src={product.image_url || 'https://images.unsplash.com/photo-1518770660439-4636190af475?w=100'} 
                                                                alt={product.name}
                                                                className="w-12 h-12 rounded object-cover"
                                                            />
                                                            <p className="text-white text-sm">{product.name}</p>
                                                        </div>
                                                    </td>
                                                    <td className="p-4">
                                                        <span className="text-[#00FFFF] text-sm">{product.category}</span>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white font-bold">${product.price.toFixed(2)}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className={`text-sm ${product.stock > 0 ? 'text-green-400' : 'text-red-400'}`}>
                                                            {product.stock}
                                                        </p>
                                                    </td>
                                                    <td className="p-4">
                                                        <div className="flex gap-2">
                                                            <Button 
                                                                size="sm" 
                                                                variant="ghost"
                                                                onClick={() => editProduct(product)}
                                                                className="text-[#00FFFF] hover:bg-[#00FFFF]/20"
                                                            >
                                                                <Edit className="w-4 h-4" />
                                                            </Button>
                                                            <Button 
                                                                size="sm" 
                                                                variant="ghost"
                                                                onClick={() => deleteProduct(product.id)}
                                                                className="text-red-400 hover:bg-red-400/20"
                                                            >
                                                                <Trash2 className="w-4 h-4" />
                                                            </Button>
                                                        </div>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>

                    {/* Services Tab */}
                    <TabsContent value="services">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Servicios ({services.length})</CardTitle>
                                    <Dialog open={serviceDialogOpen} onOpenChange={setServiceDialogOpen}>
                                        <DialogTrigger asChild>
                                            <Button 
                                                className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90"
                                                onClick={() => {
                                                    setEditingService(null);
                                                    setServiceForm({ name: '', description: '', price_per_hour: '', image_url: '', features: '' });
                                                }}
                                            >
                                                <Plus className="w-4 h-4 mr-2" />
                                                Nuevo Servicio
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="bg-black/95 border-white/10 max-w-sm md:max-w-md w-[95vw] max-h-[90vh] overflow-y-auto">
                                            <DialogHeader>
                                                <DialogTitle className="text-white text-lg md:text-xl">
                                                    {editingService ? 'Editar Servicio' : 'Nuevo Servicio'}
                                                </DialogTitle>
                                            </DialogHeader>
                                            <form onSubmit={handleServiceSubmit} className="space-y-3 md:space-y-4">
                                                <div>
                                                    <Label className="text-white/70 text-sm">Nombre</Label>
                                                    <Input
                                                        value={serviceForm.name}
                                                        onChange={(e) => setServiceForm({...serviceForm, name: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Descripcin</Label>
                                                    <Textarea
                                                        value={serviceForm.description}
                                                        onChange={(e) => setServiceForm({...serviceForm, description: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Precio por Hora ($)</Label>
                                                    <Input
                                                        type="number"
                                                        step="0.01"
                                                        value={serviceForm.price_per_hour}
                                                        onChange={(e) => setServiceForm({...serviceForm, price_per_hour: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Imagen del Servicio</Label>
                                                    <div className="flex flex-col gap-2 md:gap-3">
                                                        <div className="flex flex-col sm:flex-row gap-2">
                                                            <Input
                                                                type="file"
                                                                accept="image/*"
                                                                onChange={(e) => handleFileSelect(e, 'service')}
                                                                className="cyber-input flex-1 text-xs"
                                                                disabled={uploadingImage}
                                                            />
                                                            {uploadingImage && <span className="text-[#00FFFF] text-xs self-center">Cargando...</span>}
                                                        </div>
                                                        {(imagePreview.service || serviceForm.image_url) && (
                                                            <img 
                                                                src={imagePreview.service || serviceForm.image_url} 
                                                                alt="Preview" 
                                                                className="w-full h-24 md:h-32 object-cover rounded border border-white/20"
                                                            />
                                                        )}
                                                        <div className="text-white/50 text-xs">O pegue una URL:</div>
                                                        <Input
                                                            value={serviceForm.image_url}
                                                            onChange={(e) => setServiceForm({...serviceForm, image_url: e.target.value})}
                                                            className="cyber-input text-sm"
                                                            placeholder="https://..."
                                                        />
                                                    </div>
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Caractersticas (separadas por coma)</Label>
                                                    <Textarea
                                                        value={serviceForm.features}
                                                        onChange={(e) => setServiceForm({...serviceForm, features: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        placeholder="Feature 1, Feature 2, Feature 3"
                                                    />
                                                </div>
                                                <Button type="submit" className="w-full bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-sm">
                                                    {editingService ? 'Actualizar' : 'Crear'} Servicio
                                                </Button>
                                            </form>
                                        </DialogContent>
                                    </Dialog>
                                </div>
                            </CardHeader>
                            <CardContent className="p-0">
                                <div className="overflow-x-auto">
                                    <table className="w-full">
                                        <thead>
                                            <tr className="border-b border-white/10">
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Servicio</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Precio/Hora</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Caractersticas</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Estado</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {services.map((service) => (
                                                <tr key={service.id} className="border-b border-white/5 hover:bg-white/5">
                                                    <td className="p-4">
                                                        <div className="flex items-center gap-3">
                                                            <img 
                                                                src={service.image_url || 'https://images.unsplash.com/photo-1593640408182-31c70c8268f5?w=100'} 
                                                                alt={service.name}
                                                                className="w-12 h-12 rounded object-cover"
                                                            />
                                                            <div>
                                                                <p className="text-white text-sm">{service.name}</p>
                                                                <p className="text-white/50 text-xs line-clamp-1">{service.description}</p>
                                                            </div>
                                                        </div>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-[#00FFFF] font-bold">${service.price_per_hour.toFixed(2)}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white/60 text-xs">{(service.features || []).slice(0, 2).join(', ')}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <span className={`px-2 py-1 rounded text-xs ${service.is_active ? 'bg-green-500/20 text-green-400' : 'bg-red-500/20 text-red-400'}`}>
                                                            {service.is_active ? 'Activo' : 'Inactivo'}
                                                        </span>
                                                    </td>
                                                    <td className="p-4">
                                                        <div className="flex gap-2">
                                                            <Button 
                                                                size="sm" 
                                                                variant="ghost"
                                                                onClick={() => editService(service)}
                                                                className="text-[#00FFFF] hover:bg-[#00FFFF]/20"
                                                            >
                                                                <Edit className="w-4 h-4" />
                                                            </Button>
                                                            <Button 
                                                                size="sm" 
                                                                variant="ghost"
                                                                onClick={() => deleteService(service.id)}
                                                                className="text-red-400 hover:bg-red-400/20"
                                                            >
                                                                <Trash2 className="w-4 h-4" />
                                                            </Button>
                                                        </div>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>

                    {/* Categories Tab */}
                    <TabsContent value="categories">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Categoras ({categories.length})</CardTitle>
                                    <Dialog open={categoryDialogOpen} onOpenChange={setCategoryDialogOpen}>
                                        <DialogTrigger asChild>
                                            <Button 
                                                className="bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white"
                                                onClick={() => {
                                                    setEditingCategory(null);
                                                    setCategoryForm({ name: '', description: '' });
                                                }}
                                            >
                                                <Plus className="w-4 h-4 mr-2" />
                                                Nueva Categora
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="bg-black border-white/20">
                                            <DialogHeader>
                                                <DialogTitle className="text-white">
                                                    {editingCategory ? 'Editar Categora' : 'Agregar Categora'}
                                                </DialogTitle>
                                            </DialogHeader>
                                            <form onSubmit={handleCategorySubmit} className="space-y-4">
                                                <div>
                                                    <Label className="text-white/70">Nombre</Label>
                                                    <Input
                                                        value={categoryForm.name}
                                                        onChange={(e) => setCategoryForm({...categoryForm, name: e.target.value})}
                                                        className="cyber-input"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70">Descripcin</Label>
                                                    <Textarea
                                                        value={categoryForm.description}
                                                        onChange={(e) => setCategoryForm({...categoryForm, description: e.target.value})}
                                                        className="cyber-input"
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70">Imagen de Categora</Label>
                                                    <div className="flex flex-col gap-2">
                                                        <div className="flex gap-2">
                                                            <Input
                                                                type="file"
                                                                accept="image/*"
                                                                onChange={(e) => handleFileSelect(e, 'category')}
                                                                className="cyber-input flex-1 text-xs"
                                                                disabled={uploadingImage}
                                                            />
                                                            {uploadingImage && <span className="text-[#00FFFF] text-xs self-center">Cargando...</span>}
                                                        </div>
                                                        {(imagePreview.category || categoryForm.image_url) && (
                                                            <img 
                                                                src={imagePreview.category || categoryForm.image_url} 
                                                                alt="Preview" 
                                                                className="w-full h-24 object-cover rounded border border-white/20"
                                                            />
                                                        )}
                                                    </div>
                                                </div>
                                                <div className="flex gap-3">
                                                    <Button type="submit" className="flex-1 bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white">
                                                        {editingCategory ? 'Actualizar' : 'Crear'}
                                                    </Button>
                                                    <Button 
                                                        type="button" 
                                                        variant="outline" 
                                                        className="flex-1"
                                                        onClick={() => {
                                                            setCategoryDialogOpen(false);
                                                            setCategoryForm({ name: '', description: '', image_url: '' });
                                                        }}
                                                    >
                                                        Cancelar
                                                    </Button>
                                                </div>
                                            </form>
                                        </DialogContent>
                                    </Dialog>
                                </div>
                            </CardHeader>
                            <CardContent className="p-6">
                                <div className="overflow-x-auto">
                                    <table className="w-full text-sm">
                                        <thead>
                                            <tr className="border-b border-white/10">
                                                <th className="text-left py-3 px-4 text-white/70">Imagen</th>
                                                <th className="text-left py-3 px-4 text-white/70">Nombre</th>
                                                <th className="text-left py-3 px-4 text-white/70">Descripcin</th>
                                                <th className="text-right py-3 px-4 text-white/70">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {categories.map(category => (
                                                <tr key={category.id} className="border-b border-white/5 hover:bg-white/5">
                                                    <td className="py-3 px-4">
                                                        {category.image_url && (
                                                            <img 
                                                                src={category.image_url} 
                                                                alt={category.name}
                                                                className="w-10 h-10 object-cover rounded"
                                                            />
                                                        )}
                                                    </td>
                                                    <td className="py-3 px-4 text-white">{category.name}</td>
                                                    <td className="py-3 px-4 text-white/60 text-xs">{category.description || '-'}</td>
                                                    <td className="py-3 px-4 text-right space-x-2">
                                                        <Button
                                                            size="sm"
                                                            variant="ghost"
                                                            className="text-[#00FFFF] hover:bg-[#00FFFF]/20"
                                                            onClick={() => editCategory(category)}
                                                        >
                                                            <Edit className="w-4 h-4" />
                                                        </Button>
                                                        <Button
                                                            size="sm"
                                                            variant="ghost"
                                                            className="text-red-500 hover:bg-red-500/20"
                                                            onClick={() => deleteCategory(category.id)}
                                                        >
                                                            <Trash2 className="w-4 h-4" />
                                                        </Button>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>

                    {/* Promotions Tab */}
                    <TabsContent value="promotions">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Promociones ({promotions.length})</CardTitle>
                                    <Dialog open={promotionDialogOpen} onOpenChange={setPromotionDialogOpen}>
                                        <DialogTrigger asChild>
                                            <Button 
                                                className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90"
                                                onClick={() => {
                                                    setEditingPromotion(null);
                                                    setPromotionForm({ 
                                                        name: '', description: '', subtitle: '', image_url: '', discount_percentage: '', discount_fixed: '', 
                                                        applicable_to: 'all', start_date: '', end_date: '', is_active: true, frequency: 'once_per_day'
                                                    });
                                                }}
                                            >
                                                <Plus className="w-4 h-4 mr-2" />
                                                Nueva Promocin
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="bg-black/95 border-white/10 max-w-sm md:max-w-md w-[95vw] max-h-[90vh] overflow-y-auto">
                                            <DialogHeader>
                                                <DialogTitle className="text-white text-lg md:text-xl">
                                                    {editingPromotion ? 'Editar Promocin' : 'Nueva Promocin'}
                                                </DialogTitle>
                                            </DialogHeader>
                                            <form onSubmit={handlePromotionSubmit} className="space-y-3 md:space-y-4">
                                                <div>
                                                    <Label className="text-white/70 text-sm">Nombre</Label>
                                                    <Input
                                                        value={promotionForm.name}
                                                        onChange={(e) => setPromotionForm({...promotionForm, name: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Descripcin</Label>
                                                    <Textarea
                                                        value={promotionForm.description}
                                                        onChange={(e) => setPromotionForm({...promotionForm, description: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Subttulo (Modal)</Label>
                                                    <Input
                                                        value={promotionForm.subtitle}
                                                        onChange={(e) => setPromotionForm({...promotionForm, subtitle: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        placeholder="Texto que aparece en la ventana emergente"
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Imagen de Promocin</Label>
                                                    <div className="flex flex-col gap-2 md:gap-3">
                                                        <div className="flex flex-col sm:flex-row gap-2">
                                                            <Input
                                                                type="file"
                                                                accept="image/*"
                                                                onChange={(e) => handleFileSelect(e, 'promotion')}
                                                                className="cyber-input flex-1 text-xs"
                                                                disabled={uploadingImage}
                                                            />
                                                            {uploadingImage && <span className="text-[#00FFFF] text-xs self-center">Cargando...</span>}
                                                        </div>
                                                        {(imagePreview.promotion || promotionForm.image_url) && (
                                                            <img 
                                                                src={imagePreview.promotion || promotionForm.image_url} 
                                                                alt="Preview" 
                                                                className="w-full h-24 md:h-32 object-cover rounded border border-white/20"
                                                            />
                                                        )}
                                                        <div className="text-white/50 text-xs">O pegue una URL:</div>
                                                        <Input
                                                            value={promotionForm.image_url}
                                                            onChange={(e) => setPromotionForm({...promotionForm, image_url: e.target.value})}
                                                            className="cyber-input text-sm"
                                                            placeholder="https://..."
                                                        />
                                                    </div>
                                                </div>
                                                <div className="grid grid-cols-2 gap-2 md:gap-3">
                                                    <div>
                                                        <Label className="text-white/70 text-xs md:text-sm">Descuento (%)</Label>
                                                        <Input
                                                            type="number"
                                                            step="0.01"
                                                            value={promotionForm.discount_percentage}
                                                            onChange={(e) => setPromotionForm({...promotionForm, discount_percentage: e.target.value})}
                                                            className="cyber-input text-sm"
                                                        />
                                                    </div>
                                                    <div>
                                                        <Label className="text-white/70 text-xs md:text-sm">Descuento ($)</Label>
                                                        <Input
                                                            type="number"
                                                            step="0.01"
                                                            value={promotionForm.discount_fixed}
                                                            onChange={(e) => setPromotionForm({...promotionForm, discount_fixed: e.target.value})}
                                                            className="cyber-input text-sm"
                                                        />
                                                    </div>
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Aplicable a</Label>
                                                    <Select value={promotionForm.applicable_to} onValueChange={(val) => setPromotionForm({...promotionForm, applicable_to: val})}>
                                                        <SelectTrigger className="cyber-input text-sm">
                                                            <SelectValue />
                                                        </SelectTrigger>
                                                        <SelectContent className="bg-black border-white/20">
                                                            <SelectItem value="all">Todos</SelectItem>
                                                            <SelectItem value="products">Productos</SelectItem>
                                                            <SelectItem value="services">Servicios</SelectItem>
                                                        </SelectContent>
                                                    </Select>
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Frecuencia</Label>
                                                    <Select value={promotionForm.frequency} onValueChange={(val) => setPromotionForm({...promotionForm, frequency: val})}>
                                                        <SelectTrigger className="cyber-input text-sm">
                                                            <SelectValue />
                                                        </SelectTrigger>
                                                        <SelectContent className="bg-black border-white/20">
                                                            <SelectItem value="per_hour">Por horas</SelectItem>
                                                            <SelectItem value="always">Siempre mostrar</SelectItem>
                                                            <SelectItem value="once_per_day">Una vez por da</SelectItem>
                                                        </SelectContent>
                                                    </Select>
                                                </div>
                                                <div className="grid grid-cols-2 gap-2 md:gap-3">
                                                    <div>
                                                        <Label className="text-white/70 text-xs md:text-sm">Fecha Inicio</Label>
                                                        <Input
                                                            type="datetime-local"
                                                            value={promotionForm.start_date}
                                                            onChange={(e) => setPromotionForm({...promotionForm, start_date: e.target.value})}
                                                            className="cyber-input text-xs md:text-sm"
                                                            required
                                                        />
                                                    </div>
                                                    <div>
                                                        <Label className="text-white/70 text-xs md:text-sm">Fecha Fin</Label>
                                                        <Input
                                                            type="datetime-local"
                                                            value={promotionForm.end_date}
                                                            onChange={(e) => setPromotionForm({...promotionForm, end_date: e.target.value})}
                                                            className="cyber-input text-xs md:text-sm"
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="flex items-center gap-2">
                                                    <Checkbox 
                                                        id="is_active"
                                                        checked={promotionForm.is_active}
                                                        onCheckedChange={(checked) => setPromotionForm({...promotionForm, is_active: checked})}
                                                    />
                                                    <Label htmlFor="is_active" className="text-white/70 cursor-pointer text-sm">Activa</Label>
                                                </div>
                                                <Button type="submit" className="w-full bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-sm">
                                                    {editingPromotion ? 'Actualizar' : 'Crear'} Promocin
                                                </Button>
                                            </form>
                                        </DialogContent>
                                    </Dialog>
                                </div>
                            </CardHeader>
                            <CardContent className="p-0">
                                <div className="overflow-x-auto">
                                    <table className="w-full">
                                        <thead>
                                            <tr className="border-b border-white/10">
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Promocin</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Descuento</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Vigencia</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Estado</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {promotions.map((promo) => (
                                                <tr key={promo.id} className="border-b border-white/5 hover:bg-white/5">
                                                    <td className="p-4">
                                                        <div>
                                                            <p className="text-white text-sm">{promo.name}</p>
                                                            <p className="text-white/50 text-xs line-clamp-1">{promo.description}</p>
                                                        </div>
                                                    </td>
                                                    <td className="p-4">
                                                        <div className="text-[#00FFFF] font-bold">
                                                            {promo.discount_percentage > 0 && `${promo.discount_percentage}%`}
                                                            {promo.discount_percentage > 0 && promo.discount_fixed && ' + '}
                                                            {promo.discount_fixed && `$${promo.discount_fixed.toFixed(2)}`}
                                                        </div>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white/60 text-xs">
                                                            {new Date(promo.start_date).toLocaleDateString()} - {new Date(promo.end_date).toLocaleDateString()}
                                                        </p>
                                                    </td>
                                                    <td className="p-4">
                                                        <span className={`px-2 py-1 rounded text-xs ${promo.is_active ? 'bg-green-500/20 text-green-400' : 'bg-red-500/20 text-red-400'}`}>
                                                            {promo.is_active ? 'Activa' : 'Inactiva'}
                                                        </span>
                                                    </td>
                                                    <td className="p-4">
                                                        <div className="flex gap-2">
                                                            <Button 
                                                                size="sm" 
                                                                variant="ghost"
                                                                onClick={() => editPromotion(promo)}
                                                                className="text-[#00FFFF] hover:bg-[#00FFFF]/20"
                                                            >
                                                                <Edit className="w-4 h-4" />
                                                            </Button>
                                                            <Button 
                                                                size="sm" 
                                                                variant="ghost"
                                                                onClick={() => deletePromotion(promo.id)}
                                                                className="text-red-400 hover:bg-red-400/20"
                                                            >
                                                                <Trash2 className="w-4 h-4" />
                                                            </Button>
                                                        </div>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>

                    {/* Users Tab */}
                    <TabsContent value="users">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Usuarios ({users.length})</CardTitle>
                                    <Dialog open={userDialogOpen} onOpenChange={setUserDialogOpen}>
                                        <DialogTrigger asChild>
                                            <Button className="bg-[#00FFFF] hover:bg-[#00FFFF]/90 text-black">
                                                <Plus className="w-4 h-4 mr-2" />
                                                Nuevo Usuario
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="bg-black/95 border-white/10 max-w-sm md:max-w-md w-[95vw] max-h-[90vh] overflow-y-auto">
                                            <DialogHeader>
                                                <DialogTitle className="text-white text-lg md:text-xl">
                                                    Agregar Nuevo Usuario
                                                </DialogTitle>
                                            </DialogHeader>
                                            <form onSubmit={handleAddUser} className="space-y-3 md:space-y-4">
                                                <div>
                                                    <Label className="text-white/70 text-sm">Nombre</Label>
                                                    <Input
                                                        value={userForm.name}
                                                        onChange={(e) => setUserForm({...userForm, name: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Email</Label>
                                                    <Input
                                                        type="email"
                                                        value={userForm.email}
                                                        onChange={(e) => setUserForm({...userForm, email: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70 text-sm">Contrasea</Label>
                                                    <Input
                                                        type="password"
                                                        value={userForm.password}
                                                        onChange={(e) => setUserForm({...userForm, password: e.target.value})}
                                                        className="cyber-input text-sm"
                                                        required
                                                    />
                                                </div>
                                                <div className="flex items-center gap-2">
                                                    <Checkbox 
                                                        id="is_admin"
                                                        checked={userForm.is_admin}
                                                        onCheckedChange={(checked) => setUserForm({...userForm, is_admin: checked})}
                                                    />
                                                    <Label htmlFor="is_admin" className="text-white/70 cursor-pointer text-sm">
                                                        Es Administrador
                                                    </Label>
                                                </div>
                                                <Button type="submit" className="w-full bg-[#00FFFF] hover:bg-[#00FFFF]/90 text-black text-sm font-semibold">
                                                    Agregar Usuario
                                                </Button>
                                            </form>
                                        </DialogContent>
                                    </Dialog>
                                </div>
                            </CardHeader>
                            <CardContent className="p-0">
                                <div className="overflow-x-auto">
                                    <table className="w-full">
                                        <thead>
                                            <tr className="border-b border-white/10">
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Usuario</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Email</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Rol</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Registro</th>
                                                <th className="text-left p-4 text-white/50 text-xs uppercase">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {users.map((u) => (
                                                <tr key={u.id} className="border-b border-white/5 hover:bg-white/5">
                                                    <td className="p-4">
                                                        <p className="text-white">{u.name}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white/60">{u.email}</p>
                                                    </td>
                                                    <td className="p-4">
                                                        <span className={`px-2 py-1 rounded text-xs ${u.is_admin ? 'bg-[#FF00FF]/20 text-[#FF00FF]' : 'bg-white/10 text-white/60'}`}>
                                                            {u.is_admin ? 'Admin' : 'Usuario'}
                                                        </span>
                                                    </td>
                                                    <td className="p-4">
                                                        <p className="text-white/40 text-sm">
                                                            {new Date(u.created_at).toLocaleDateString()}
                                                        </p>
                                                    </td>
                                                    <td className="p-4">
                                                        <div className="flex gap-2 items-center">
                                                            {user.id !== u.id && (
                                                                <>
                                                                    <Select 
                                                                        value={u.is_admin ? 'admin' : 'user'}
                                                                        onValueChange={(value) => handleToggleAdminRole(u.id, value === 'admin')}
                                                                    >
                                                                        <SelectTrigger className="w-[120px] h-8 text-xs bg-black border-white/20 cyber-input">
                                                                            <SelectValue />
                                                                        </SelectTrigger>
                                                                        <SelectContent className="bg-black border-white/20">
                                                                            <SelectItem value="user">Usuario</SelectItem>
                                                                            <SelectItem value="admin">Administrador</SelectItem>
                                                                        </SelectContent>
                                                                    </Select>
                                                                    <Button 
                                                                        size="sm" 
                                                                        variant="ghost"
                                                                        onClick={() => deleteUser(u.id)}
                                                                        className="text-red-400 hover:bg-red-400/20"
                                                                    >
                                                                        <Trash2 className="w-4 h-4" />
                                                                    </Button>
                                                                </>
                                                            )}
                                                        </div>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>

                    {/* About Us Tab */}
                    <TabsContent value="about">
                        <Card className="bg-black/50 border-white/10">
                            <CardHeader className="border-b border-white/10">
                                <div className="flex items-center justify-between">
                                    <CardTitle className="text-white">Gestin de Sobre Nosotros</CardTitle>
                                    <Dialog open={aboutDialogOpen} onOpenChange={setAboutDialogOpen}>
                                        <DialogTrigger asChild>
                                            <Button className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90">
                                                <Plus className="w-4 h-4 mr-2" />
                                                Editar Contenido
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="bg-black border-white/20 max-w-2xl max-h-[90vh] overflow-y-auto">
                                            <DialogHeader>
                                                <DialogTitle className="text-white">Editar Contenido - Sobre Nosotros</DialogTitle>
                                            </DialogHeader>
                                            <form onSubmit={handleAboutSubmit} className="space-y-4">
                                                <div>
                                                    <Label className="text-white/70">Ttulo</Label>
                                                    <Input
                                                        value={aboutForm.title}
                                                        onChange={(e) => setAboutForm({...aboutForm, title: e.target.value})}
                                                        className="cyber-input"
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70">Descripcin</Label>
                                                    <Textarea
                                                        value={aboutForm.description}
                                                        onChange={(e) => setAboutForm({...aboutForm, description: e.target.value})}
                                                        className="cyber-input"
                                                        rows={6}
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <Label className="text-white/70">Imgenes de Instalaciones</Label>
                                                    <div className="flex flex-col gap-2">
                                                        <div className="flex gap-2">
                                                            <Input
                                                                type="file"
                                                                accept="image/*"
                                                                multiple
                                                                onChange={(e) => {
                                                                    const files = e.target.files;
                                                                    if (files && files.length > 0) {
                                                                        for (let i = 0; i < files.length; i++) {
                                                                            handleFileSelect({ target: { files: { 0: files[i], length: 1 } } }, 'about');
                                                                        }
                                                                    }
                                                                }}
                                                                className="cyber-input flex-1 text-xs"
                                                                disabled={uploadingImage}
                                                            />
                                                            {uploadingImage && <span className="text-[#00FFFF] text-xs self-center">Cargando...</span>}
                                                        </div>
                                                        <p className="text-white/40 text-xs">Puedes seleccionar mltiples imgenes a la vez</p>
                                                        {/* Preview de imgenes */}
                                                        {aboutForm.images && aboutForm.images.length > 0 && (
                                                            <div className="grid grid-cols-2 gap-2">
                                                                {aboutForm.images.map((img, idx) => (
                                                                    <div key={idx} className="relative group">
                                                                        <img 
                                                                            src={img} 
                                                                            alt={`Instalacin ${idx + 1}`}
                                                                            className="w-full h-24 object-cover rounded border border-white/20"
                                                                        />
                                                                        <button
                                                                            type="button"
                                                                            onClick={() => removeImageFromAbout(idx)}
                                                                            className="absolute top-1 right-1 bg-red-500 text-white p-1 rounded opacity-0 group-hover:opacity-100 transition-opacity"
                                                                        >
                                                                            <Trash2 className="w-4 h-4" />
                                                                        </button>
                                                                    </div>
                                                                ))}
                                                            </div>
                                                        )}
                                                        {(imagePreview.about && !aboutForm.images.includes(imagePreview.about)) && (
                                                            <img 
                                                                src={imagePreview.about} 
                                                                alt="Preview" 
                                                                className="w-full h-24 object-cover rounded border border-white/20"
                                                            />
                                                        )}
                                                    </div>
                                                </div>
                                                <div className="flex gap-3">
                                                    <Button type="submit" className="flex-1 bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white">
                                                        Guardar
                                                    </Button>
                                                    <Button 
                                                        type="button" 
                                                        variant="outline" 
                                                        className="flex-1"
                                                        onClick={() => setAboutDialogOpen(false)}
                                                    >
                                                        Cancelar
                                                    </Button>
                                                </div>
                                            </form>
                                        </DialogContent>
                                    </Dialog>
                                </div>
                            </CardHeader>
                            <CardContent className="p-6">
                                <div className="bg-black/30 border border-white/10 rounded-lg p-6">
                                    <h3 className="text-xl font-bold text-white mb-3">{aboutForm.title || 'Sobre Nosotros'}</h3>
                                    <p className="text-white/60 whitespace-pre-wrap mb-6">{aboutForm.description || 'Sin contenido'}</p>
                                    {aboutForm.images && aboutForm.images.length > 0 && (
                                        <div>
                                            <p className="text-white/50 text-sm mb-3">Imgenes: {aboutForm.images.length}</p>
                                            <div className="grid grid-cols-3 gap-2">
                                                {aboutForm.images.map((img, idx) => (
                                                    <img 
                                                        key={idx}
                                                        src={img} 
                                                        alt={`Instalacin ${idx + 1}`}
                                                        className="w-full h-20 object-cover rounded border border-white/20"
                                                    />
                                                ))}
                                            </div>
                                        </div>
                                    )}
                                </div>
                            </CardContent>
                        </Card>
                    </TabsContent>
                </Tabs>
            </div>
        </div>
    );
}
