import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { Button } from '../components/ui/button';
import { Package, ArrowLeft, Clock, MapPin, Phone, FileText } from 'lucide-react';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

const statusConfig = {
    pending: { label: 'Pendiente', color: 'status-pending', icon: Clock },
    processing: { label: 'En Proceso', color: 'status-processing', icon: Package },
    shipped: { label: 'Enviado', color: 'status-shipped', icon: Package },
    completed: { label: 'Completado', color: 'status-completed', icon: Package },
    cancelled: { label: 'Cancelado', color: 'status-cancelled', icon: Package }
};

export default function MyOrders() {
    const { orderId } = useParams();
    const { token } = useAuth();
    const [orders, setOrders] = useState([]);
    const [selectedOrder, setSelectedOrder] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchOrders = async () => {
            try {
                if (orderId) {
                    const res = await axios.get(`${API}/orders/${orderId}`, {
                        headers: { Authorization: `Bearer ${token}` }
                    });
                    setSelectedOrder(res.data);
                } else {
                    const res = await axios.get(`${API}/orders`, {
                        headers: { Authorization: `Bearer ${token}` }
                    });
                    setOrders(res.data);
                }
                // Scroll al top
                window.scrollTo(0, 0);
            } catch (err) {
                console.error('Error:', err);
            } finally {
                setLoading(false);
            }
        };
        
        if (token) {
            fetchOrders();
        } else {
            setLoading(false);
        }
    }, [token, orderId]);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="loading-spinner" />
            </div>
        );
    }

    if (!token) {
        return (
            <div className="min-h-screen bg-[#030303] pt-24 flex items-center justify-center">
                <div className="text-center">
                    <Package className="w-24 h-24 text-white/20 mx-auto mb-6" />
                    <h2 className="text-2xl font-bold text-white mb-4">Inicia Sesin</h2>
                    <p className="text-white/50 mb-8">Para ver tus pedidos necesitas iniciar sesin</p>
                    <Link to="/login">
                        <Button className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90">
                            Iniciar Sesin
                        </Button>
                    </Link>
                </div>
            </div>
        );
    }

    // Order Detail View
    if (selectedOrder) {
        const status = statusConfig[selectedOrder.status];
        return (
            <div className="min-h-screen bg-[#030303] pt-24">
                <div className="max-w-4xl mx-auto px-4 py-12">
                    <Link to="/mis-pedidos" className="flex items-center gap-2 text-white/50 hover:text-white mb-8">
                        <ArrowLeft className="w-5 h-5" />
                        Volver a mis pedidos
                    </Link>

                    <Card className="bg-black/50 border-white/10 mb-6">
                        <CardHeader className="border-b border-white/10">
                            <div className="flex items-center justify-between">
                                <CardTitle className="text-white" style={{ fontFamily: 'Orbitron' }}>
                                    Pedido #{selectedOrder.id.slice(0, 8)}
                                </CardTitle>
                                <span className={`px-3 py-1 rounded-full text-sm ${status.color}`}>
                                    {status.label}
                                </span>
                            </div>
                        </CardHeader>
                        <CardContent className="p-6">
                            {/* Order Status Timeline */}
                            <div className="mb-8">
                                <h3 className="text-white/70 text-sm font-semibold mb-4">ESTADO DEL PEDIDO</h3>
                                <div className="flex items-center justify-between relative">
                                    <div className="absolute top-4 left-0 right-0 h-0.5 bg-white/10" />
                                    {['pending', 'processing', 'shipped', 'completed'].map((s, idx) => {
                                        const isActive = Object.keys(statusConfig).indexOf(selectedOrder.status) >= idx;
                                        const isCurrent = selectedOrder.status === s;
                                        return (
                                            <div key={s} className="relative flex flex-col items-center z-10">
                                                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                                                    isActive ? 'bg-[#00FFFF]' : 'bg-white/10'
                                                } ${isCurrent ? 'ring-2 ring-[#00FFFF] ring-offset-2 ring-offset-black' : ''}`}>
                                                    {isActive && <span className="text-black text-xs"></span>}
                                                </div>
                                                <span className={`text-xs mt-2 ${isActive ? 'text-[#00FFFF]' : 'text-white/40'}`}>
                                                    {statusConfig[s].label}
                                                </span>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>

                            {/* Order Info */}
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
                                <div className="space-y-4">
                                    <div className="flex items-start gap-3">
                                        <MapPin className="w-5 h-5 text-[#00FFFF] mt-0.5" />
                                        <div>
                                            <p className="text-white/40 text-xs uppercase">Direccin de envo</p>
                                            <p className="text-white">{selectedOrder.shipping_address}</p>
                                        </div>
                                    </div>
                                    <div className="flex items-start gap-3">
                                        <Phone className="w-5 h-5 text-[#00FFFF] mt-0.5" />
                                        <div>
                                            <p className="text-white/40 text-xs uppercase">Telfono</p>
                                            <p className="text-white">{selectedOrder.phone}</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="space-y-4">
                                    <div className="flex items-start gap-3">
                                        <Clock className="w-5 h-5 text-[#FF00FF] mt-0.5" />
                                        <div>
                                            <p className="text-white/40 text-xs uppercase">Fecha del pedido</p>
                                            <p className="text-white">{new Date(selectedOrder.created_at).toLocaleString()}</p>
                                        </div>
                                    </div>
                                    {selectedOrder.notes && (
                                        <div className="flex items-start gap-3">
                                            <FileText className="w-5 h-5 text-[#FF00FF] mt-0.5" />
                                            <div>
                                                <p className="text-white/40 text-xs uppercase">Notas</p>
                                                <p className="text-white">{selectedOrder.notes}</p>
                                            </div>
                                        </div>
                                    )}
                                </div>
                            </div>

                            {/* Order Items */}
                            <div>
                                <h3 className="text-white/70 text-sm font-semibold mb-4">PRODUCTOS</h3>
                                <div className="space-y-3">
                                    {selectedOrder.items.map((item, idx) => (
                                        <div key={idx} className="flex items-center justify-between py-3 border-b border-white/10">
                                            <div>
                                                <p className="text-white font-semibold">{item.product_name}</p>
                                                <p className="text-white/40 text-sm">Cantidad: {item.quantity}</p>
                                            </div>
                                            <p className="text-[#00FFFF] font-bold">
                                                ${(item.price * item.quantity).toFixed(2)}
                                            </p>
                                        </div>
                                    ))}
                                </div>
                                <div className="flex justify-between items-center pt-6 mt-6 border-t border-white/20">
                                    <span className="text-white font-bold text-xl">TOTAL</span>
                                    <span className="text-[#00FFFF] font-bold text-2xl">${selectedOrder.total.toFixed(2)}</span>
                                </div>
                            </div>
                        </CardContent>
                    </Card>
                </div>
            </div>
        );
    }

    // Orders List View
    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            <div className="max-w-5xl mx-auto px-4 py-12">
                <h1 
                    className="text-3xl md:text-4xl font-bold text-white mb-8"
                    style={{ fontFamily: 'Orbitron' }}
                    data-testid="my-orders-title"
                >
                    <span className="text-[#00FFFF]">MIS</span> PEDIDOS
                </h1>

                {orders.length === 0 ? (
                    <div className="text-center py-20">
                        <Package className="w-24 h-24 text-white/20 mx-auto mb-6" />
                        <h2 className="text-xl font-bold text-white mb-4">No tienes pedidos</h2>
                        <p className="text-white/50 mb-8">Visita nuestra tienda y realiza tu primer pedido!</p>
                        <Link to="/tienda">
                            <Button className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90">
                                Ir a la Tienda
                            </Button>
                        </Link>
                    </div>
                ) : (
                    <div className="space-y-4 stagger-children">
                        {orders.map((order, idx) => {
                            const status = statusConfig[order.status];
                            return (
                                <Link 
                                    key={order.id} 
                                    to={`/mis-pedidos/${order.id}`}
                                    data-testid={`order-item-${idx}`}
                                >
                                    <Card className="bg-black/50 border-white/10 hover:border-[#00FFFF]/50 transition-colors cyber-card">
                                        <CardContent className="p-6">
                                            <div className="flex items-center justify-between mb-4">
                                                <div>
                                                    <p className="text-white font-bold">Pedido #{order.id.slice(0, 8)}</p>
                                                    <p className="text-white/40 text-sm">
                                                        {new Date(order.created_at).toLocaleDateString()}
                                                    </p>
                                                </div>
                                                <span className={`px-3 py-1 rounded-full text-sm ${status.color}`}>
                                                    {status.label}
                                                </span>
                                            </div>
                                            <div className="flex items-center justify-between">
                                                <p className="text-white/60 text-sm">
                                                    {order.items.length} producto(s)
                                                </p>
                                                <p className="text-[#00FFFF] font-bold text-xl">
                                                    ${order.total.toFixed(2)}
                                                </p>
                                            </div>
                                        </CardContent>
                                    </Card>
                                </Link>
                            );
                        })}
                    </div>
                )}
            </div>
        </div>
    );
}
