import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Button } from '../components/ui/button';
import { ChevronRight, Gamepad2, Trophy, Monitor, Users } from 'lucide-react';
import PromoModal from '../components/PromoModal';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;
const LOGO_URL = "https://customer-assets.emergentagent.com/job_gaming-central-16/artifacts/w7offuan_560255988_829968086211881_2841994104288046581_n.jpg";

export default function Home() {
    const [services, setServices] = useState([]);

    useEffect(() => {
        const fetchServices = async () => {
            try {
                const res = await axios.get(`${API}/services`);
                setServices(res.data.slice(0, 4));
            } catch (err) {
                console.error('Error fetching services:', err);
            }
        };
        fetchServices();
        // Scroll al top
        window.scrollTo(0, 0);
    }, []);

    const features = [
        { icon: Gamepad2, title: 'PCs Gaming', desc: 'RTX 4070, 32GB RAM, monitores 165Hz' },
        { icon: Trophy, title: 'Torneos', desc: 'Competiciones semanales con premios' },
        { icon: Monitor, title: 'Venta', desc: 'Accesorios y equipos de alta calidad' },
        { icon: Users, title: 'Comunidad', desc: 'nete a nuestra comunidad gaming' },
    ];

    return (
        <div className="min-h-screen bg-[#030303]">
            {/* Promo Modal */}
            <PromoModal />
            
            {/* Hero Section */}
            <section className="relative min-h-screen flex items-center justify-center overflow-hidden">
                {/* Background effects */}
                <div className="absolute inset-0">
                    <div className="absolute inset-0 bg-gradient-to-b from-black via-transparent to-black z-10" />
                    <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-[#00FFFF]/10 rounded-full blur-[100px]" />
                    <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-[#FF00FF]/10 rounded-full blur-[100px]" />
                </div>

                <div className="relative z-20 text-center px-4 max-w-5xl mx-auto">
                    {/* Logo */}
                    <div className="mb-8 flex justify-center">
                        <img 
                            src={LOGO_URL} 
                            alt="TROYANO Cyber CLUB" 
                            className="w-48 h-48 md:w-64 md:h-64 object-contain flicker"
                            data-testid="hero-logo"
                        />
                    </div>

                    {/* Title */}
                    <h1 
                        className="text-5xl md:text-7xl lg:text-8xl font-black tracking-tighter mb-4 glitch"
                        style={{ fontFamily: 'Orbitron' }}
                        data-testid="hero-title"
                    >
                        <span className="text-[#00FFFF] neon-cyan">TROYANO</span>
                    </h1>
                    <h2 
                        className="text-2xl md:text-4xl font-bold text-white/60 tracking-[0.3em] mb-8"
                        style={{ fontFamily: 'Orbitron' }}
                    >
                        CYBER CLUB
                    </h2>

                    {/* Subtitle */}
                    <p className="text-lg md:text-xl text-white/50 max-w-2xl mx-auto mb-12 leading-relaxed">
                        El santuario definitivo para gamers. PCs de alto rendimiento, 
                        torneos competitivos y la mejor tecnologa a tu alcance.
                    </p>

                    {/* CTA Buttons */}
                    <div className="flex flex-col sm:flex-row gap-4 justify-center">
                        <Link to="/servicios">
                            <Button 
                                className="cyber-btn bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90 px-8 py-6 text-lg font-bold"
                                data-testid="cta-services"
                            >
                                Ver Servicios
                                <ChevronRight className="ml-2 w-5 h-5" />
                            </Button>
                        </Link>
                        <Link to="/tienda">
                            <Button 
                                variant="outline"
                                className="border-[#FF00FF] text-[#FF00FF] hover:bg-[#FF00FF] hover:text-white px-8 py-6 text-lg font-bold"
                                data-testid="cta-store"
                            >
                                Tienda
                            </Button>
                        </Link>
                    </div>
                </div>

                {/* Scroll indicator */}
                <div className="absolute bottom-8 left-1/2 -translate-x-1/2 animate-bounce">
                    <div className="w-6 h-10 border-2 border-white/30 rounded-full flex justify-center">
                        <div className="w-1.5 h-3 bg-[#00FFFF] rounded-full mt-2" />
                    </div>
                </div>
            </section>

            {/* Features Section */}
            <section className="py-24 px-4">
                <div className="max-w-7xl mx-auto">
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-6 stagger-children">
                        {features.map((feature, idx) => (
                            <div 
                                key={idx}
                                className="cyber-card bg-white/5 border border-white/10 p-6 rounded-xl text-center hover:border-[#00FFFF]/50"
                                data-testid={`feature-${idx}`}
                            >
                                <feature.icon className="w-10 h-10 text-[#00FFFF] mx-auto mb-4" />
                                <h3 className="text-white font-bold mb-2">{feature.title}</h3>
                                <p className="text-white/50 text-sm">{feature.desc}</p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Services Preview */}
            {services.length > 0 && (
                <section className="py-24 px-4 bg-gradient-to-b from-transparent via-[#00FFFF]/5 to-transparent">
                    <div className="max-w-7xl mx-auto">
                        <div className="text-center mb-16">
                            <h2 
                                className="text-4xl md:text-5xl font-bold text-white mb-4"
                                style={{ fontFamily: 'Orbitron' }}
                                data-testid="services-title"
                            >
                                NUESTROS <span className="text-[#FF00FF]">SERVICIOS</span>
                            </h2>
                            <p className="text-white/50 max-w-xl mx-auto">
                                Experimenta el gaming como nunca antes
                            </p>
                        </div>

                        <div className="bento-grid">
                            {services.map((service, idx) => (
                                <div 
                                    key={service.id}
                                    className="relative overflow-hidden rounded-xl border border-white/10 bg-black group cyber-card"
                                    data-testid={`service-card-${idx}`}
                                >
                                    <img 
                                        src={service.image_url} 
                                        alt={service.name}
                                        className="absolute inset-0 w-full h-full object-cover opacity-50 group-hover:opacity-70 transition-opacity duration-500"
                                    />
                                    <div className="absolute inset-0 bg-gradient-to-t from-black via-black/50 to-transparent" />
                                    <div className="relative z-10 h-full flex flex-col justify-end p-6">
                                        <h3 className="text-xl md:text-2xl font-bold text-white mb-2">{service.name}</h3>
                                        <p className="text-white/60 text-sm mb-3 line-clamp-2">{service.description}</p>
                                        <div className="flex items-center justify-between">
                                            <span className="text-[#00FFFF] font-bold">${service.price_per_hour}/hora</span>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>

                        <div className="text-center mt-12">
                            <Link to="/servicios">
                                <Button 
                                    variant="outline" 
                                    className="border-[#00FFFF] text-[#00FFFF] hover:bg-[#00FFFF] hover:text-black"
                                    data-testid="view-all-services"
                                >
                                    Ver Todos los Servicios
                                    <ChevronRight className="ml-2 w-4 h-4" />
                                </Button>
                            </Link>
                        </div>
                    </div>
                </section>
            )}

            {/* CTA Section */}
            <section className="py-24 px-4">
                <div className="max-w-4xl mx-auto text-center">
                    <div className="relative p-12 rounded-2xl border border-white/10 bg-gradient-to-br from-[#00FFFF]/10 via-transparent to-[#FF00FF]/10 overflow-hidden">
                        <div className="absolute top-0 right-0 w-64 h-64 bg-[#FF00FF]/20 rounded-full blur-[80px]" />
                        <div className="absolute bottom-0 left-0 w-64 h-64 bg-[#00FFFF]/20 rounded-full blur-[80px]" />
                        
                        <div className="relative z-10">
                            <h2 
                                className="text-3xl md:text-4xl font-bold text-white mb-4"
                                style={{ fontFamily: 'Orbitron' }}
                            >
                                LISTO PARA <span className="text-[#00FFFF]">JUGAR</span>?
                            </h2>
                            <p className="text-white/60 mb-8 max-w-xl mx-auto">
                                Vistanos hoy y descubre por qu somos el centro gaming #1 de la ciudad
                            </p>
                            <Link to="/servicios">
                                <Button 
                                    className="bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white px-8 py-6 text-lg font-bold"
                                    data-testid="cta-final"
                                >
                                    Reservar Ahora
                                </Button>
                            </Link>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
