import { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Label } from '../components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../components/ui/tabs';
import { LogIn, UserPlus, ArrowLeft, Terminal } from 'lucide-react';
import { toast } from 'sonner';

const LOGO_URL = "https://customer-assets.emergentagent.com/job_gaming-central-16/artifacts/w7offuan_560255988_829968086211881_2841994104288046581_n.jpg";

export default function Login() {
    const { login, register } = useAuth();
    const navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const [loginData, setLoginData] = useState({ email: '', password: '' });
    const [registerData, setRegisterData] = useState({ name: '', email: '', password: '', confirmPassword: '' });

    useEffect(() => {
        // Scroll al top cuando se monta el componente
        window.scrollTo(0, 0);
    }, []);

    const handleLogin = async (e) => {
        e.preventDefault();
        setLoading(true);
        try {
            await login(loginData.email, loginData.password);
            toast.success('Bienvenido de vuelta!');
            navigate('/');
        } catch (error) {
            toast.error(error.response?.data?.detail || 'Error al iniciar sesin');
        } finally {
            setLoading(false);
        }
    };

    const handleRegister = async (e) => {
        e.preventDefault();
        if (registerData.password !== registerData.confirmPassword) {
            toast.error('Las contraseas no coinciden');
            return;
        }
        setLoading(true);
        try {
            await register(registerData.name, registerData.email, registerData.password);
            toast.success('Cuenta creada exitosamente!');
            navigate('/');
        } catch (error) {
            toast.error(error.response?.data?.detail || 'Error al crear cuenta');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-[#030303] flex items-center justify-center px-4 py-24">
            {/* Background effects */}
            <div className="fixed inset-0 pointer-events-none">
                <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-[#00FFFF]/5 rounded-full blur-[100px]" />
                <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-[#FF00FF]/5 rounded-full blur-[100px]" />
            </div>

            <div className="w-full max-w-md relative z-10">
                <Link to="/" className="flex items-center gap-2 text-white/50 hover:text-white mb-8">
                    <ArrowLeft className="w-5 h-5" />
                    Volver al inicio
                </Link>

                <div className="text-center mb-8">
                    <img src={LOGO_URL} alt="TROYANO" className="w-20 h-20 mx-auto mb-4" />
                    <h1 className="text-2xl font-bold text-white" style={{ fontFamily: 'Orbitron' }}>
                        <span className="text-[#00FFFF]">SYSTEM</span> ACCESS
                    </h1>
                </div>

                <Card className="bg-black/80 border-white/10 backdrop-blur-xl">
                    <CardHeader className="border-b border-white/10">
                        <div className="flex items-center gap-2 text-[#00FFFF]">
                            <Terminal className="w-5 h-5" />
                            <span className="font-mono text-sm">troyano@auth:~$</span>
                        </div>
                    </CardHeader>
                    <CardContent className="p-6">
                        <Tabs defaultValue="login" className="w-full">
                            <TabsList className="grid w-full grid-cols-2 bg-white/5 mb-6">
                                <TabsTrigger 
                                    value="login"
                                    className="data-[state=active]:bg-[#00FFFF] data-[state=active]:text-black"
                                    data-testid="login-tab"
                                >
                                    Ingresar
                                </TabsTrigger>
                                <TabsTrigger 
                                    value="register"
                                    className="data-[state=active]:bg-[#FF00FF] data-[state=active]:text-white"
                                    data-testid="register-tab"
                                >
                                    Registrarse
                                </TabsTrigger>
                            </TabsList>

                            <TabsContent value="login">
                                <form onSubmit={handleLogin} className="space-y-4">
                                    <div>
                                        <Label className="text-white/70 font-mono text-sm">EMAIL:</Label>
                                        <Input
                                            type="email"
                                            value={loginData.email}
                                            onChange={(e) => setLoginData({...loginData, email: e.target.value})}
                                            className="cyber-input font-mono"
                                            placeholder="user@troyano.com"
                                            required
                                            data-testid="login-email"
                                        />
                                    </div>
                                    <div>
                                        <Label className="text-white/70 font-mono text-sm">PASSWORD:</Label>
                                        <Input
                                            type="password"
                                            value={loginData.password}
                                            onChange={(e) => setLoginData({...loginData, password: e.target.value})}
                                            className="cyber-input font-mono"
                                            placeholder=""
                                            required
                                            data-testid="login-password"
                                        />
                                    </div>
                                    <Button
                                        type="submit"
                                        className="w-full bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90 font-bold"
                                        disabled={loading}
                                        data-testid="login-submit"
                                    >
                                        {loading ? (
                                            <div className="loading-spinner w-5 h-5" />
                                        ) : (
                                            <>
                                                <LogIn className="w-4 h-4 mr-2" />
                                                INICIAR SESIN
                                            </>
                                        )}
                                    </Button>
                                </form>
                            </TabsContent>

                            <TabsContent value="register">
                                <form onSubmit={handleRegister} className="space-y-4">
                                    <div>
                                        <Label className="text-white/70 font-mono text-sm">NOMBRE:</Label>
                                        <Input
                                            type="text"
                                            value={registerData.name}
                                            onChange={(e) => setRegisterData({...registerData, name: e.target.value})}
                                            className="cyber-input font-mono"
                                            placeholder="Tu nombre"
                                            required
                                            data-testid="register-name"
                                        />
                                    </div>
                                    <div>
                                        <Label className="text-white/70 font-mono text-sm">EMAIL:</Label>
                                        <Input
                                            type="email"
                                            value={registerData.email}
                                            onChange={(e) => setRegisterData({...registerData, email: e.target.value})}
                                            className="cyber-input font-mono"
                                            placeholder="user@troyano.com"
                                            required
                                            data-testid="register-email"
                                        />
                                    </div>
                                    <div>
                                        <Label className="text-white/70 font-mono text-sm">PASSWORD:</Label>
                                        <Input
                                            type="password"
                                            value={registerData.password}
                                            onChange={(e) => setRegisterData({...registerData, password: e.target.value})}
                                            className="cyber-input font-mono"
                                            placeholder=""
                                            required
                                            minLength={6}
                                            data-testid="register-password"
                                        />
                                    </div>
                                    <div>
                                        <Label className="text-white/70 font-mono text-sm">CONFIRM PASSWORD:</Label>
                                        <Input
                                            type="password"
                                            value={registerData.confirmPassword}
                                            onChange={(e) => setRegisterData({...registerData, confirmPassword: e.target.value})}
                                            className="cyber-input font-mono"
                                            placeholder=""
                                            required
                                            data-testid="register-confirm-password"
                                        />
                                    </div>
                                    <Button
                                        type="submit"
                                        className="w-full bg-[#FF00FF] text-white hover:bg-[#FF00FF]/90 font-bold"
                                        disabled={loading}
                                        data-testid="register-submit"
                                    >
                                        {loading ? (
                                            <div className="loading-spinner w-5 h-5" />
                                        ) : (
                                            <>
                                                <UserPlus className="w-4 h-4 mr-2" />
                                                CREAR CUENTA
                                            </>
                                        )}
                                    </Button>
                                </form>
                            </TabsContent>
                        </Tabs>
                    </CardContent>
                </Card>

                <p className="text-center text-white/30 text-sm mt-6 font-mono">
                    TROYANO CYBER CLUB // SECURE ACCESS v2.0
                </p>
            </div>
        </div>
    );
}
