import { useEffect, useState } from 'react';
import axios from 'axios';
import { MapPin, Phone, Mail } from 'lucide-react';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function AboutUs() {
    const [aboutData, setAboutData] = useState(null);
    const [businessInfo, setBusinessInfo] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [aboutRes, infoRes] = await Promise.all([
                    axios.get(`${API}/about`),
                    axios.get(`${API}/info`)
                ]);
                setAboutData(aboutRes.data);
                setBusinessInfo(infoRes.data);
            } catch (err) {
                console.error('Error:', err);
            } finally {
                setLoading(false);
            }
        };
        fetchData();
        // Scroll al top
        window.scrollTo(0, 0);
    }, []);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-[#030303]">
                <div className="loading-spinner" />
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            {/* Hero */}
            <section className="py-16 px-4">
                <div className="max-w-7xl mx-auto text-center">
                    <h1 
                        className="text-4xl md:text-6xl font-black text-white mb-4"
                        style={{ fontFamily: 'Orbitron' }}
                    >
                        SOBRE <span className="text-[#00FFFF] neon-cyan">NOSOTROS</span>
                    </h1>
                    <p className="text-white/50 text-lg max-w-2xl mx-auto">
                        Conoce nuestra historia y nuestras instalaciones
                    </p>
                </div>
            </section>

            {/* About Content */}
            {aboutData && (
                <section className="py-12 px-4">
                    <div className="max-w-5xl mx-auto">
                        <div className="bg-black/50 border border-white/10 rounded-xl p-8 md:p-12 mb-12">
                            <h2 className="text-3xl font-bold text-white mb-6" style={{ fontFamily: 'Orbitron' }}>
                                {aboutData.title}
                            </h2>
                            <p className="text-white/70 text-lg leading-relaxed whitespace-pre-wrap">
                                {aboutData.description}
                            </p>
                        </div>
                    </div>
                </section>
            )}

            {/* Facilities Gallery */}
            <section className="py-12 px-4">
                <div className="max-w-7xl mx-auto">
                    <h2 
                        className="text-3xl md:text-4xl font-bold text-center text-white mb-12"
                        style={{ fontFamily: 'Orbitron' }}
                    >
                        NUESTRAS <span className="text-[#00FFFF]">INSTALACIONES</span>
                    </h2>
                    
                    {aboutData && aboutData.images && aboutData.images.length > 0 ? (
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 stagger-children">
                            {aboutData.images.map((image, idx) => (
                                <div 
                                    key={idx}
                                    className="relative aspect-video rounded-xl overflow-hidden border border-white/10 cyber-card hover:border-[#FF00FF]/50 transition-all duration-300 group cursor-pointer"
                                >
                                    <img 
                                        src={image} 
                                        alt={`Instalacin ${idx + 1}`}
                                        className="absolute inset-0 w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                                    />
                                    <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-end justify-center pb-4">
                                        <span className="text-[#00FFFF] font-semibold text-sm">Instalacin {idx + 1}</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    ) : (
                        <div className="text-center py-12">
                            <p className="text-white/50">No hay imgenes de instalaciones disponibles</p>
                        </div>
                    )}
                </div>
            </section>

            {/* Contact Info */}
            {businessInfo && (
                <section className="py-12 px-4 bg-gradient-to-b from-transparent via-[#00FFFF]/5 to-transparent">
                    <div className="max-w-5xl mx-auto">
                        <h2 className="text-3xl font-bold text-center text-white mb-12" style={{ fontFamily: 'Orbitron' }}>
                            CONTACTO E <span className="text-[#FF00FF]">INFORMACIN</span>
                        </h2>
                        
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                            {/* Address */}
                            <div className="bg-black/50 border border-white/10 rounded-xl p-8 text-center">
                                <MapPin className="w-12 h-12 text-[#00FFFF] mx-auto mb-4" />
                                <h3 className="text-lg font-bold text-white mb-3">Ubicacin</h3>
                                <p className="text-white/60">
                                    {businessInfo.address}
                                </p>
                            </div>

                            {/* Phone */}
                            <div className="bg-black/50 border border-white/10 rounded-xl p-8 text-center">
                                <Phone className="w-12 h-12 text-[#FF00FF] mx-auto mb-4" />
                                <h3 className="text-lg font-bold text-white mb-3">Telfono</h3>
                                <p className="text-white/60">
                                    {businessInfo.phone}
                                </p>
                            </div>

                            {/* Hours */}
                            <div className="bg-black/50 border border-white/10 rounded-xl p-8 text-center">
                                <Mail className="w-12 h-12 text-[#00FFFF] mx-auto mb-4" />
                                <h3 className="text-lg font-bold text-white mb-3">Horarios</h3>
                                <p className="text-white/60 text-sm">
                                    L-V: {businessInfo.hours.weekdays}<br />
                                    S-D: {businessInfo.hours.weekends}
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            )}
        </div>
    );
}
