import { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useCart } from '../context/CartContext';
import { Button } from '../components/ui/button';
import { Card, CardContent } from '../components/ui/card';
import { ChevronLeft, Plus, Minus, ShoppingCart } from 'lucide-react';
import { toast } from 'sonner';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function ProductDetail() {
    const { productId } = useParams();
    const navigate = useNavigate();
    const { addToCart } = useCart();
    const [product, setProduct] = useState(null);
    const [loading, setLoading] = useState(true);
    const [quantity, setQuantity] = useState(1);

    useEffect(() => {
        const fetchProduct = async () => {
            try {
                const res = await axios.get(`${API}/products/${productId}`);
                setProduct(res.data);
                // Scroll al top
                window.scrollTo(0, 0);
            } catch (err) {
                console.error('Error:', err);
                toast.error('Producto no encontrado');
                navigate('/tienda');
            } finally {
                setLoading(false);
            }
        };
        fetchProduct();
    }, [productId, navigate]);

    const handleAddToCart = () => {
        if (!product || product.stock <= 0) {
            toast.error('Producto sin stock');
            return;
        }
        
        for (let i = 0; i < quantity; i++) {
            addToCart(product);
        }
        toast.success(`${quantity} x ${product.name} aadido al carrito`);
        setQuantity(1);
    };

    const handleQuantityChange = (newQuantity) => {
        if (newQuantity < 1) return;
        if (newQuantity > product.stock) {
            toast.error(`Solo hay ${product.stock} en stock`);
            return;
        }
        setQuantity(newQuantity);
    };

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="loading-spinner" />
            </div>
        );
    }

    if (!product) {
        return (
            <div className="min-h-screen bg-[#030303] pt-24 flex items-center justify-center">
                <p className="text-white/50">Producto no encontrado</p>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            {/* Navigation */}
            <section className="py-6 px-4 border-b border-white/10">
                <div className="max-w-7xl mx-auto">
                    <Button
                        variant="ghost"
                        onClick={() => navigate('/tienda')}
                        className="text-white/50 hover:text-white"
                    >
                        <ChevronLeft className="w-5 h-5 mr-2" />
                        Volver a la Tienda
                    </Button>
                </div>
            </section>

            {/* Product Details */}
            <section className="py-12 px-4">
                <div className="max-w-7xl mx-auto">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
                        {/* Product Image */}
                        <div className="flex items-center justify-center">
                            <Card className="bg-black border-white/10 overflow-hidden w-full">
                                <img
                                    src={product.image_url || 'https://images.unsplash.com/photo-1518770660439-4636190af475?w=800'}
                                    alt={product.name}
                                    className="w-full h-full object-cover"
                                />
                            </Card>
                        </div>

                        {/* Product Info */}
                        <div className="flex flex-col justify-between">
                            {/* Header */}
                            <div className="mb-8">
                                <div className="inline-block mb-4 text-[#00FFFF] text-sm bg-[#00FFFF]/10 px-3 py-1 rounded border border-[#00FFFF]/30">
                                    {product.category}
                                </div>
                                <h1
                                    className="text-4xl md:text-5xl font-black text-white mb-4"
                                    style={{ fontFamily: 'Orbitron' }}
                                >
                                    {product.name}
                                </h1>
                                <p className="text-white/60 text-lg leading-relaxed">
                                    {product.description}
                                </p>
                            </div>

                            {/* Price and Stock */}
                            <div className="bg-black/50 border border-white/10 rounded-lg p-6 mb-8">
                                <div className="flex items-baseline gap-4 mb-4">
                                    <span className="text-5xl font-bold text-[#00FFFF]">
                                        ${product.price.toFixed(2)}
                                    </span>
                                </div>
                                <div className="flex items-center gap-3">
                                    <div
                                        className={`w-3 h-3 rounded-full ${
                                            product.stock > 10
                                                ? 'bg-green-500'
                                                : product.stock > 0
                                                ? 'bg-yellow-500'
                                                : 'bg-red-500'
                                        }`}
                                    />
                                    <span className="text-white/60">
                                        {product.stock > 0 ? (
                                            <>Stock: <span className="text-white">{product.stock} unidades</span></>
                                        ) : (
                                            <span className="text-red-500">Sin stock</span>
                                        )}
                                    </span>
                                </div>
                            </div>

                            {/* Specifications */}
                            {product.specs && Object.keys(product.specs).length > 0 && (
                                <div className="bg-black/50 border border-white/10 rounded-lg p-6 mb-8">
                                    <h3 className="text-xl font-bold text-white mb-4">
                                        Especificaciones
                                    </h3>
                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                        {Object.entries(product.specs).map(([key, value]) => (
                                            <div key={key} className="border-b border-white/5 pb-3">
                                                <p className="text-white/50 text-sm capitalize mb-1">
                                                    {key.replace(/_/g, ' ')}
                                                </p>
                                                <p className="text-white font-semibold">
                                                    {value}
                                                </p>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            )}

                            {/* Quantity and Add to Cart */}
                            <div className="flex flex-col gap-4">
                                <div className="flex items-center gap-4">
                                    <span className="text-white/60">Cantidad:</span>
                                    <div className="flex items-center border border-white/20 rounded-lg bg-black/50">
                                        <button
                                            onClick={() => handleQuantityChange(quantity - 1)}
                                            className="p-2 text-white/60 hover:text-white disabled:text-white/20"
                                            disabled={quantity <= 1 || product.stock <= 0}
                                        >
                                            <Minus className="w-4 h-4" />
                                        </button>
                                        <span className="px-4 py-2 text-white font-semibold min-w-[3rem] text-center">
                                            {quantity}
                                        </span>
                                        <button
                                            onClick={() => handleQuantityChange(quantity + 1)}
                                            className="p-2 text-white/60 hover:text-white disabled:text-white/20"
                                            disabled={quantity >= product.stock || product.stock <= 0}
                                        >
                                            <Plus className="w-4 h-4" />
                                        </button>
                                    </div>
                                </div>

                                <Button
                                    onClick={handleAddToCart}
                                    disabled={product.stock <= 0}
                                    className="w-full bg-[#FF00FF] hover:bg-[#FF00FF]/80 text-white font-bold py-6 text-lg"
                                >
                                    <ShoppingCart className="w-5 h-5 mr-2" />
                                    Aadir al Carrito
                                </Button>
                            </div>

                            {/* Additional Info */}
                            <div className="mt-8 pt-8 border-t border-white/10">
                                <div className="grid grid-cols-3 gap-4 text-center">
                                    <div>
                                        <p className="text-white/40 text-sm mb-1">Envo</p>
                                        <p className="text-white text-sm">A nivel nacional</p>
                                    </div>
                                    <div>
                                        <p className="text-white/40 text-sm mb-1">Garanta</p>
                                        <p className="text-white text-sm">Segn fabricante</p>
                                    </div>
                                    <div>
                                        <p className="text-white/40 text-sm mb-1">Soporte</p>
                                        <p className="text-white text-sm">24/7 disponible</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Related Products Section (Optional) */}
            <section className="py-12 px-4 border-t border-white/10 bg-black/50">
                <div className="max-w-7xl mx-auto text-center">
                    <h2 className="text-2xl font-bold text-white mb-4">
                        Te interesa explorar ms productos?
                    </h2>
                    <Button
                        onClick={() => navigate('/tienda')}
                        className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90 font-bold"
                    >
                        Ver todos los productos
                    </Button>
                </div>
            </section>
        </div>
    );
}
