import { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useCart } from '../context/CartContext';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Textarea } from '../components/ui/textarea';
import { Label } from '../components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { Minus, Plus, Trash2, ShoppingBag, ArrowLeft, CreditCard } from 'lucide-react';
import { toast } from 'sonner';
import { useEffect } from 'react';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function Cart() {
    const { cart, updateQuantity, removeFromCart, clearCart, getTotal } = useCart();
    const { user, token } = useAuth();
    const navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const [formData, setFormData] = useState({
        shipping_address: '',
        phone: '',
        notes: ''
    });

    useEffect(() => {
        // Scroll al top cuando se monta el componente
        window.scrollTo(0, 0);
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (!user) {
            toast.error('Debes iniciar sesin para realizar un pedido');
            navigate('/login');
            return;
        }

        if (cart.length === 0) {
            toast.error('El carrito est vaco');
            return;
        }

        if (!formData.shipping_address || !formData.phone) {
            toast.error('Por favor completa los datos de envo');
            return;
        }

        setLoading(true);
        try {
            const orderData = {
                items: cart.map(item => ({
                    product_id: item.product.id,
                    quantity: item.quantity
                })),
                shipping_address: formData.shipping_address,
                phone: formData.phone,
                notes: formData.notes || null
            };

            const response = await axios.post(`${API}/orders`, orderData, {
                headers: { Authorization: `Bearer ${token}` }
            });

            clearCart();
            toast.success('Pedido realizado con xito!');
            navigate(`/mis-pedidos/${response.data.id}`);
        } catch (error) {
            console.error('Error creating order:', error);
            toast.error(error.response?.data?.detail || 'Error al procesar el pedido');
        } finally {
            setLoading(false);
        }
    };

    if (cart.length === 0) {
        return (
            <div className="min-h-screen bg-[#030303] pt-24 flex items-center justify-center">
                <div className="text-center">
                    <ShoppingBag className="w-24 h-24 text-white/20 mx-auto mb-6" />
                    <h2 className="text-2xl font-bold text-white mb-4" style={{ fontFamily: 'Orbitron' }}>
                        Carrito Vaco
                    </h2>
                    <p className="text-white/50 mb-8">No hay productos en tu carrito</p>
                    <Link to="/tienda">
                        <Button className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90" data-testid="continue-shopping">
                            <ArrowLeft className="w-4 h-4 mr-2" />
                            Ir a la Tienda
                        </Button>
                    </Link>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            <div className="max-w-7xl mx-auto px-4 py-12">
                <div className="flex items-center gap-4 mb-8">
                    <Link to="/tienda" className="text-white/50 hover:text-white">
                        <ArrowLeft className="w-6 h-6" />
                    </Link>
                    <h1 
                        className="text-3xl md:text-4xl font-bold text-white"
                        style={{ fontFamily: 'Orbitron' }}
                        data-testid="cart-title"
                    >
                        <span className="text-[#00FFFF]">MI</span> CARRITO
                    </h1>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    {/* Cart Items */}
                    <div className="lg:col-span-2 space-y-4">
                        {cart.map((item, idx) => (
                            <Card 
                                key={item.product.id}
                                className="bg-black/50 border-white/10"
                                data-testid={`cart-item-${idx}`}
                            >
                                <CardContent className="p-4 flex gap-4">
                                    <img 
                                        src={item.product.image_url || 'https://images.unsplash.com/photo-1518770660439-4636190af475?w=200'}
                                        alt={item.product.name}
                                        className="w-24 h-24 object-cover rounded-lg"
                                    />
                                    <div className="flex-1">
                                        <h3 className="text-white font-semibold mb-1">{item.product.name}</h3>
                                        <p className="text-white/40 text-sm mb-2">{item.product.category}</p>
                                        <p className="text-[#00FFFF] font-bold">${item.product.price.toFixed(2)}</p>
                                    </div>
                                    <div className="flex flex-col items-end justify-between">
                                        <button
                                            onClick={() => removeFromCart(item.product.id)}
                                            className="text-red-400 hover:text-red-300 p-1"
                                            data-testid={`remove-item-${idx}`}
                                        >
                                            <Trash2 className="w-5 h-5" />
                                        </button>
                                        <div className="flex items-center gap-2 bg-white/5 rounded-lg p-1">
                                            <button
                                                onClick={() => updateQuantity(item.product.id, item.quantity - 1)}
                                                className="p-1 hover:bg-white/10 rounded"
                                                data-testid={`decrease-qty-${idx}`}
                                            >
                                                <Minus className="w-4 h-4 text-white/60" />
                                            </button>
                                            <span className="text-white w-8 text-center">{item.quantity}</span>
                                            <button
                                                onClick={() => updateQuantity(item.product.id, item.quantity + 1)}
                                                className="p-1 hover:bg-white/10 rounded"
                                                disabled={item.quantity >= item.product.stock}
                                                data-testid={`increase-qty-${idx}`}
                                            >
                                                <Plus className="w-4 h-4 text-white/60" />
                                            </button>
                                        </div>
                                        <p className="text-white font-semibold">
                                            ${(item.product.price * item.quantity).toFixed(2)}
                                        </p>
                                    </div>
                                </CardContent>
                            </Card>
                        ))}
                    </div>

                    {/* Order Summary */}
                    <div className="lg:col-span-1">
                        <Card className="bg-black/50 border-white/10 sticky top-24">
                            <CardHeader>
                                <CardTitle className="text-white flex items-center gap-2" style={{ fontFamily: 'Orbitron' }}>
                                    <CreditCard className="w-5 h-5 text-[#FF00FF]" />
                                    Resumen del Pedido
                                </CardTitle>
                            </CardHeader>
                            <CardContent>
                                <form onSubmit={handleSubmit} className="space-y-4">
                                    <div className="space-y-4 mb-6">
                                        <div>
                                            <Label className="text-white/70">Direccin de Envo *</Label>
                                            <Textarea
                                                value={formData.shipping_address}
                                                onChange={(e) => setFormData({...formData, shipping_address: e.target.value})}
                                                placeholder="Calle, nmero, ciudad..."
                                                className="bg-black/50 border-white/20 text-white mt-1"
                                                required
                                                data-testid="shipping-address"
                                            />
                                        </div>
                                        <div>
                                            <Label className="text-white/70">Telfono *</Label>
                                            <Input
                                                type="tel"
                                                value={formData.phone}
                                                onChange={(e) => setFormData({...formData, phone: e.target.value})}
                                                placeholder="+1 234 567 890"
                                                className="bg-black/50 border-white/20 text-white mt-1"
                                                required
                                                data-testid="phone-input"
                                            />
                                        </div>
                                        <div>
                                            <Label className="text-white/70">Notas (opcional)</Label>
                                            <Textarea
                                                value={formData.notes}
                                                onChange={(e) => setFormData({...formData, notes: e.target.value})}
                                                placeholder="Instrucciones especiales..."
                                                className="bg-black/50 border-white/20 text-white mt-1"
                                                data-testid="notes-input"
                                            />
                                        </div>
                                    </div>

                                    <div className="border-t border-white/10 pt-4 space-y-2">
                                        <div className="flex justify-between text-white/60">
                                            <span>Subtotal</span>
                                            <span>${getTotal().toFixed(2)}</span>
                                        </div>
                                        <div className="flex justify-between text-white/60">
                                            <span>Envo</span>
                                            <span className="text-[#00FFFF]">Por calcular</span>
                                        </div>
                                        <div className="flex justify-between text-white font-bold text-xl pt-2 border-t border-white/10">
                                            <span>Total</span>
                                            <span className="text-[#00FFFF]">${getTotal().toFixed(2)}</span>
                                        </div>
                                    </div>

                                    {!user && (
                                        <p className="text-yellow-400 text-sm text-center">
                                            Debes iniciar sesin para realizar el pedido
                                        </p>
                                    )}

                                    <Button
                                        type="submit"
                                        className="w-full bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white py-6 text-lg font-bold"
                                        disabled={loading || !user}
                                        data-testid="checkout-button"
                                    >
                                        {loading ? (
                                            <div className="loading-spinner w-6 h-6" />
                                        ) : (
                                            <>
                                                <ShoppingBag className="w-5 h-5 mr-2" />
                                                {user ? 'Confirmar Pedido' : 'Inicia Sesin'}
                                            </>
                                        )}
                                    </Button>
                                </form>
                            </CardContent>
                        </Card>
                    </div>
                </div>
            </div>
        </div>
    );
}
