import { useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { useCart } from '../context/CartContext';
import { ShoppingCart, User, Menu, X, LogOut, Settings, Package } from 'lucide-react';
import { Button } from './ui/button';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuSeparator,
    DropdownMenuTrigger,
} from './ui/dropdown-menu';

const LOGO_URL = "https://customer-assets.emergentagent.com/job_gaming-central-16/artifacts/w7offuan_560255988_829968086211881_2841994104288046581_n.jpg";

export const Navbar = () => {
    const { user, logout, isAdmin } = useAuth();
    const { getItemCount } = useCart();
    const navigate = useNavigate();
    const location = useLocation();
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
    const itemCount = getItemCount();

    const navLinks = [
        { path: '/', label: 'Inicio' },
        { path: '/servicios', label: 'Servicios' },
        { path: '/sobre-nosotros', label: 'Sobre Nosotros' },
        { path: '/tienda', label: 'Tienda' },
    ];

    const isActive = (path) => location.pathname === path;

    return (
        <nav className="fixed top-0 left-0 right-0 z-50 bg-black/80 backdrop-blur-xl border-b border-white/10">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex items-center justify-between h-16">
                    {/* Logo */}
                    <Link to="/" className="flex items-center gap-3" data-testid="nav-logo">
                        <img src={LOGO_URL} alt="TROYANO" className="h-10 w-10 object-contain" />
                        <span className="font-bold text-lg tracking-wider hidden sm:block" style={{ fontFamily: 'Orbitron' }}>
                            <span className="text-[#00FFFF]">TROYANO</span>
                            <span className="text-white/60 ml-1">CYBER</span>
                        </span>
                    </Link>

                    {/* Desktop Navigation */}
                    <div className="hidden md:flex items-center gap-8">
                        {navLinks.map(link => (
                            <Link
                                key={link.path}
                                to={link.path}
                                className={`nav-link text-sm font-semibold uppercase tracking-widest ${
                                    isActive(link.path) ? 'text-[#00FFFF]' : ''
                                }`}
                                data-testid={`nav-${link.label.toLowerCase()}`}
                            >
                                {link.label}
                            </Link>
                        ))}
                    </div>

                    {/* Right side */}
                    <div className="flex items-center gap-4">
                        {/* Cart */}
                        <Link to="/carrito" className="relative p-2" data-testid="nav-cart">
                            <ShoppingCart className="w-6 h-6 text-white/70 hover:text-[#00FFFF] transition-colors" />
                            {itemCount > 0 && (
                                <span className="cart-badge">{itemCount}</span>
                            )}
                        </Link>

                        {/* User menu */}
                        {user ? (
                            <DropdownMenu>
                                <DropdownMenuTrigger asChild>
                                    <Button variant="ghost" className="flex items-center gap-2" data-testid="user-menu-trigger">
                                        <div className="w-8 h-8 rounded-full bg-gradient-to-br from-[#00FFFF] to-[#FF00FF] flex items-center justify-center">
                                            <span className="text-black font-bold text-sm">
                                                {user.name.charAt(0).toUpperCase()}
                                            </span>
                                        </div>
                                        <span className="hidden sm:block text-sm">{user.name}</span>
                                    </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end" className="w-48 bg-black/95 border-white/10">
                                    <DropdownMenuItem onClick={() => navigate('/mis-pedidos')} data-testid="menu-orders">
                                        <Package className="w-4 h-4 mr-2" />
                                        Mis Pedidos
                                    </DropdownMenuItem>
                                    {isAdmin && (
                                        <>
                                            <DropdownMenuSeparator />
                                            <DropdownMenuItem onClick={() => navigate('/admin')} data-testid="menu-admin">
                                                <Settings className="w-4 h-4 mr-2" />
                                                Panel Admin
                                            </DropdownMenuItem>
                                        </>
                                    )}
                                    <DropdownMenuSeparator />
                                    <DropdownMenuItem onClick={logout} className="text-red-400" data-testid="menu-logout">
                                        <LogOut className="w-4 h-4 mr-2" />
                                        Cerrar Sesin
                                    </DropdownMenuItem>
                                </DropdownMenuContent>
                            </DropdownMenu>
                        ) : (
                            <Button
                                onClick={() => navigate('/login')}
                                className="bg-transparent border border-[#00FFFF] text-[#00FFFF] hover:bg-[#00FFFF] hover:text-black"
                                data-testid="nav-login"
                            >
                                <User className="w-4 h-4 mr-2" />
                                Ingresar
                            </Button>
                        )}

                        {/* Mobile menu button */}
                        <button
                            className="md:hidden p-2"
                            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                            data-testid="mobile-menu-toggle"
                        >
                            {mobileMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
                        </button>
                    </div>
                </div>

                {/* Mobile Navigation */}
                {mobileMenuOpen && (
                    <div className="md:hidden py-4 border-t border-white/10">
                        {navLinks.map(link => (
                            <Link
                                key={link.path}
                                to={link.path}
                                className={`block py-3 text-sm font-semibold uppercase tracking-widest ${
                                    isActive(link.path) ? 'text-[#00FFFF]' : 'text-white/70'
                                }`}
                                onClick={() => setMobileMenuOpen(false)}
                                data-testid={`mobile-nav-${link.label.toLowerCase()}`}
                            >
                                {link.label}
                            </Link>
                        ))}
                    </div>
                )}
            </div>
        </nav>
    );
};
