import { useEffect, useState } from 'react';
import axios from 'axios';
import { Clock, Check, MapPin, Phone } from 'lucide-react';
import { Card, CardContent } from '../components/ui/card';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function Services() {
    const [services, setServices] = useState([]);
    const [businessInfo, setBusinessInfo] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [servicesRes, infoRes] = await Promise.all([
                    axios.get(`${API}/services`),
                    axios.get(`${API}/info`)
                ]);
                setServices(servicesRes.data);
                setBusinessInfo(infoRes.data);
            } catch (err) {
                console.error('Error:', err);
            } finally {
                setLoading(false);
            }
        };
        fetchData();
        // Scroll al top
        window.scrollTo(0, 0);
    }, []);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="loading-spinner" />
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            {/* Hero */}
            <section className="py-16 px-4">
                <div className="max-w-7xl mx-auto text-center">
                    <h1 
                        className="text-4xl md:text-6xl font-black text-white mb-4"
                        style={{ fontFamily: 'Orbitron' }}
                        data-testid="services-hero-title"
                    >
                        NUESTROS <span className="text-[#00FFFF] neon-cyan">SERVICIOS</span>
                    </h1>
                    <p className="text-white/50 text-lg max-w-2xl mx-auto">
                        Equipos de ltima generacin, ambiente competitivo y la mejor experiencia gaming
                    </p>
                </div>
            </section>

            {/* Services Grid */}
            <section className="py-12 px-4">
                <div className="max-w-7xl mx-auto">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8 stagger-children">
                        {services.map((service, idx) => (
                            <Card 
                                key={service.id}
                                className="bg-black/50 border-white/10 overflow-hidden cyber-card hover:border-[#00FFFF]/50"
                                data-testid={`service-item-${idx}`}
                            >
                                <div className="relative h-64">
                                    <img 
                                        src={service.image_url} 
                                        alt={service.name}
                                        className="absolute inset-0 w-full h-full object-cover"
                                    />
                                    <div className="absolute inset-0 bg-gradient-to-t from-black via-black/50 to-transparent" />
                                    <div className="absolute top-4 right-4 bg-[#FF00FF] text-white px-4 py-2 rounded-full text-sm font-bold">
                                        ${service.price_per_hour}/hora
                                    </div>
                                </div>
                                <CardContent className="p-6">
                                    <h3 className="text-2xl font-bold text-white mb-3" style={{ fontFamily: 'Orbitron' }}>
                                        {service.name}
                                    </h3>
                                    <p className="text-white/60 mb-6">
                                        {service.description}
                                    </p>
                                    <div className="grid grid-cols-2 gap-3">
                                        {service.features.map((feature, fidx) => (
                                            <div key={fidx} className="flex items-center gap-2">
                                                <Check className="w-4 h-4 text-[#00FFFF]" />
                                                <span className="text-white/70 text-sm">{feature}</span>
                                            </div>
                                        ))}
                                    </div>
                                </CardContent>
                            </Card>
                        ))}
                    </div>
                </div>
            </section>

            {/* Pricing Table */}
            <section className="py-24 px-4 bg-gradient-to-b from-transparent via-[#FF00FF]/5 to-transparent">
                <div className="max-w-5xl mx-auto">
                    <h2 
                        className="text-3xl md:text-4xl font-bold text-center text-white mb-12"
                        style={{ fontFamily: 'Orbitron' }}
                        data-testid="pricing-title"
                    >
                        <span className="text-[#FF00FF]">TARIFAS</span> Y HORARIOS
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                        {/* Pricing */}
                        <div className="bg-black/50 border border-white/10 rounded-xl p-8">
                            <h3 className="text-xl font-bold text-[#00FFFF] mb-6 flex items-center gap-2">
                                <Clock className="w-5 h-5" />
                                Tarifas por Hora
                            </h3>
                            <div className="space-y-4">
                                {services.map((service) => (
                                    <div key={service.id} className="flex justify-between items-center py-3 border-b border-white/10">
                                        <span className="text-white/80">{service.name}</span>
                                        <span className="text-[#00FFFF] font-bold">${service.price_per_hour}</span>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Hours */}
                        {businessInfo && (
                            <div className="bg-black/50 border border-white/10 rounded-xl p-8">
                                <h3 className="text-xl font-bold text-[#FF00FF] mb-6 flex items-center gap-2">
                                    <Clock className="w-5 h-5" />
                                    Horarios de Atencin
                                </h3>
                                <div className="space-y-4 mb-8">
                                    <div className="flex justify-between items-center py-3 border-b border-white/10">
                                        <span className="text-white/80">Lunes - Viernes</span>
                                        <span className="text-white font-semibold">{businessInfo.hours.weekdays}</span>
                                    </div>
                                    <div className="flex justify-between items-center py-3 border-b border-white/10">
                                        <span className="text-white/80">Sbado - Domingo</span>
                                        <span className="text-white font-semibold">{businessInfo.hours.weekends}</span>
                                    </div>
                                </div>

                                <div className="space-y-3">
                                    <div className="flex items-start gap-3 text-white/60">
                                        <MapPin className="w-5 h-5 text-[#00FFFF] flex-shrink-0 mt-1" />
                                        <span className="text-sm">{businessInfo.address}</span>
                                    </div>
                                    <div className="flex items-center gap-3 text-white/60">
                                        <Phone className="w-5 h-5 text-[#00FFFF]" />
                                        <span className="text-sm">{businessInfo.phone}</span>
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </section>
        </div>
    );
}
