import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useCart } from '../context/CartContext';
import { Button } from '../components/ui/button';
import { Card, CardContent } from '../components/ui/card';
import { ShoppingCart, Plus, Search } from 'lucide-react';
import { Input } from '../components/ui/input';
import { toast } from 'sonner';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function Store() {
    const [products, setProducts] = useState([]);
    const [categories, setCategories] = useState([]);
    const [loading, setLoading] = useState(true);
    const [search, setSearch] = useState('');
    const { addToCart, getItemCount } = useCart();

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [productsRes, categoriesRes] = await Promise.all([
                    axios.get(`${API}/products`),
                    axios.get(`${API}/categories`)
                ]);
                setProducts(productsRes.data);
                setCategories(categoriesRes.data);
                // Scroll al top
                window.scrollTo(0, 0);
            } catch (err) {
                console.error('Error:', err);
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, []);

    const handleAddToCart = (product) => {
        if (product.stock <= 0) {
            toast.error('Producto sin stock');
            return;
        }
        addToCart(product);
        toast.success(`${product.name} aadido al carrito`);
    };

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="loading-spinner" />
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#030303] pt-24">
            {/* Hero */}
            <section className="py-12 px-4 border-b border-white/10">
                <div className="max-w-7xl mx-auto">
                    <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-6">
                        <div>
                            <h1 
                                className="text-4xl md:text-5xl font-black text-white mb-2"
                                style={{ fontFamily: 'Orbitron' }}
                                data-testid="store-title"
                            >
                                <span className="text-[#FF00FF] neon-magenta">CYBER</span> TIENDA
                            </h1>
                            <p className="text-white/50">
                                Las piezas que necesitas para tu PC
                            </p>
                        </div>
                        
                        <Link to="/carrito">
                            <Button 
                                className="bg-[#00FFFF] text-black hover:bg-[#00FFFF]/90 relative"
                                data-testid="cart-button"
                            >
                                <ShoppingCart className="w-5 h-5 mr-2" />
                                Ver Carrito
                                {getItemCount() > 0 && (
                                    <span className="absolute -top-2 -right-2 bg-[#FF00FF] text-white text-xs w-6 h-6 rounded-full flex items-center justify-center">
                                        {getItemCount()}
                                    </span>
                                )}
                            </Button>
                        </Link>
                    </div>
                </div>
            </section>

            {/* Filters */}
            <section className="py-6 px-4 border-b border-white/10 bg-black/50">
                <div className="max-w-7xl mx-auto flex flex-col md:flex-row gap-4">
                    <div className="relative flex-1">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-white/40" />
                        <Input
                            type="text"
                            placeholder="Buscar productos..."
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                            className="pl-10 bg-black/50 border-white/20 text-white placeholder:text-white/40"
                            data-testid="search-input"
                        />
                    </div>
                </div>
            </section>

            {/* Products by Category */}
            <section className="py-12 px-4">
                <div className="max-w-7xl mx-auto">
                    {categories.length === 0 ? (
                        <div className="text-center py-20">
                            <p className="text-white/50 text-lg">No hay categoras disponibles</p>
                        </div>
                    ) : (
                        categories.map((cat) => {
                            const categoryProducts = search 
                                ? products.filter(p => 
                                    p.category === cat.name &&
                                    (p.name.toLowerCase().includes(search.toLowerCase()) ||
                                     p.description.toLowerCase().includes(search.toLowerCase()))
                                  )
                                : products.filter(p => p.category === cat.name);
                            
                            if (categoryProducts.length === 0) return null;
                            
                            return (
                                <div key={cat.id} className="mb-16">
                                    {/* Category Header */}
                                    <div className="flex items-center gap-4 mb-8 pb-4 border-b border-white/10">
                                        {cat.image_url && (
                                            <img 
                                                src={cat.image_url} 
                                                alt={cat.name}
                                                className="w-16 h-16 object-cover rounded border border-white/20"
                                            />
                                        )}
                                        <div>
                                            <h2 
                                                className="text-3xl font-bold text-white"
                                                style={{ fontFamily: 'Orbitron' }}
                                            >
                                                {cat.name}
                                            </h2>
                                            {cat.description && (
                                                <p className="text-white/50 text-sm mt-1">
                                                    {cat.description}
                                                </p>
                                            )}
                                        </div>
                                        <div className="ml-auto text-[#00FFFF] text-sm">
                                            {categoryProducts.length} producto{categoryProducts.length !== 1 ? 's' : ''}
                                        </div>
                                    </div>

                                    {/* Products Grid */}
                                    <div className="product-grid stagger-children mb-12">
                                        {categoryProducts.map((product, idx) => (
                                            <Link
                                                key={product.id}
                                                to={`/producto/${product.id}`}
                                                className="no-underline"
                                            >
                                                <Card 
                                                    className="bg-black border-white/10 overflow-hidden cyber-card hover:border-[#FF00FF]/50 flex flex-col h-full cursor-pointer transition-all duration-300 hover:shadow-lg hover:shadow-[#FF00FF]/20"
                                                    data-testid={`product-card-${idx}`}
                                                >
                                                    <div className="relative aspect-square">
                                                        <img 
                                                            src={product.image_url || 'https://images.unsplash.com/photo-1518770660439-4636190af475?w=400'} 
                                                            alt={product.name}
                                                            className="absolute inset-0 w-full h-full object-cover"
                                                        />
                                                        {product.stock <= 0 && (
                                                            <div className="absolute inset-0 bg-black/70 flex items-center justify-center">
                                                                <span className="text-red-500 font-bold uppercase">Sin Stock</span>
                                                            </div>
                                                        )}
                                                    </div>
                                                    <CardContent className="p-4 flex flex-col flex-1">
                                                        <h3 className="text-white font-bold text-sm mb-2 line-clamp-2">
                                                            {product.name}
                                                        </h3>
                                                        <p className="text-white/40 text-xs mb-4 line-clamp-2 flex-1">
                                                            {product.description}
                                                        </p>
                                                        
                                                        {product.specs && (
                                                            <div className="flex flex-wrap gap-1 mb-4">
                                                                {Object.entries(product.specs).slice(0, 2).map(([key, value]) => (
                                                                    <span key={key} className="text-[10px] text-white/50 bg-white/5 px-2 py-1 rounded">
                                                                        {value}
                                                                    </span>
                                                                ))}
                                                            </div>
                                                        )}
                                                        
                                                        <div className="flex items-center justify-between mt-auto">
                                                            <div>
                                                                <span className="text-[#00FFFF] font-bold text-xl">
                                                                    ${product.price.toFixed(2)}
                                                                </span>
                                                                <span className="text-white/30 text-xs ml-2">
                                                                    Stock: {product.stock}
                                                                </span>
                                                            </div>
                                                            <Button
                                                                size="sm"
                                                                onClick={(e) => {
                                                                    e.preventDefault();
                                                                    handleAddToCart(product);
                                                                }}
                                                                disabled={product.stock <= 0}
                                                                className="bg-[#FF00FF] hover:bg-[#FF00FF]/80 text-white"
                                                                data-testid={`add-to-cart-${idx}`}
                                                            >
                                                                <Plus className="w-4 h-4" />
                                                            </Button>
                                                        </div>
                                                    </CardContent>
                                                </Card>
                                            </Link>
                                        ))}
                                    </div>
                                </div>
                            );
                        })
                    )}
                    
                    {/* No products found message */}
                    {categories.length > 0 && search && products.filter(p => 
                        p.name.toLowerCase().includes(search.toLowerCase()) ||
                        p.description.toLowerCase().includes(search.toLowerCase())
                    ).length === 0 && (
                        <div className="text-center py-20">
                            <p className="text-white/50 text-lg">No se encontraron productos con "{search}"</p>
                        </div>
                    )}
                </div>
            </section>
        </div>
    );
}
