import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { X, Zap } from 'lucide-react';
import { Button } from './ui/button';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function PromoModal() {
    const [promotion, setPromotion] = useState(null);
    const [isOpen, setIsOpen] = useState(true);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchActivePromo = async () => {
            try {
                const res = await axios.get(`${API}/promotions`);
                // Obtener la primera promocin activa
                const activePromos = res.data.filter(p => {
                    const now = new Date();
                    const start = new Date(p.start_date);
                    const end = new Date(p.end_date);
                    return p.is_active && now >= start && now <= end;
                });
                
                if (activePromos.length > 0) {
                    const promo = activePromos[0];
                    setPromotion(promo);
                    
                    // Verificar si debe mostrarse segn la frecuencia
                    const storageKey = `promoShown_${promo.id}`;
                    const lastShownTime = localStorage.getItem(storageKey);
                    const now = new Date();
                    let shouldShow = true;
                    
                    if (lastShownTime) {
                        const lastShown = new Date(lastShownTime);
                        
                        switch(promo.frequency) {
                            case 'per_hour':
                                // Mostrar si pas ms de 1 hora
                                shouldShow = (now - lastShown) > (60 * 60 * 1000);
                                break;
                            case 'once_per_day':
                                // Mostrar si pas ms de 24 horas
                                shouldShow = (now - lastShown) > (24 * 60 * 60 * 1000);
                                break;
                            case 'always':
                                // Mostrar siempre
                                shouldShow = true;
                                break;
                            default:
                                shouldShow = true;
                        }
                    }
                    
                    setIsOpen(shouldShow);
                }
            } catch (err) {
                console.error('Error:', err);
            }
        };

        fetchActivePromo();
    }, []);

    if (!promotion || !isOpen) return null;

    const discountText = promotion.discount_percentage > 0 
        ? `${promotion.discount_percentage}%`
        : promotion.discount_fixed > 0
        ? `$${promotion.discount_fixed.toFixed(2)}`
        : 'Especial';

    const handleClose = () => {
        setIsOpen(false);
        localStorage.setItem(`promoShown_${promotion.id}`, new Date().toISOString());
    };

    const handleViewDetails = () => {
        handleClose();
        navigate(`/promocion/${promotion.id}`);
    };

    return (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-3 sm:p-4">
            <div className="relative bg-gradient-to-br from-[#030303] to-black border-2 border-[#FF00FF] rounded-lg shadow-2xl w-full max-w-xs sm:max-w-md animate-in fade-in zoom-in duration-300 max-h-[90vh] overflow-y-auto">
                {/* Close Button */}
                <button
                    onClick={handleClose}
                    className="absolute top-3 right-3 sm:top-4 sm:right-4 text-white/60 hover:text-white transition-colors z-10 bg-black/50 rounded-full p-1 hover:bg-black/70"
                >
                    <X className="w-5 h-5 sm:w-6 sm:h-6" />
                </button>

                {/* Content */}
                <div className="p-4 sm:p-6 md:p-8">
                    {/* Image - if available */}
                    {promotion.image_url && (
                        <div className="mb-3 sm:mb-4 rounded-lg overflow-hidden border border-[#FF00FF]/30 -mx-4 sm:-mx-6 md:-mx-8 -mt-4 sm:-mt-6 md:-mt-8">
                            <img 
                                src={promotion.image_url} 
                                alt={promotion.name}
                                className="w-full h-32 sm:h-40 md:h-48 object-cover"
                            />
                        </div>
                    )}

                    {/* Header */}
                    <div className="flex items-start gap-2 sm:gap-3 mb-3 sm:mb-4">
                        <Zap className="w-5 h-5 sm:w-6 sm:h-6 text-[#FF00FF] flex-shrink-0 mt-1" />
                    </div>

                    {/* Title */}
                    <h3 className="text-base sm:text-xl md:text-2xl font-bold text-[#00FFFF] mb-2 sm:mb-3 neon-cyan line-clamp-2">
                        {promotion.name}
                    </h3>

                    {/* Subtitle - if available */}
                    {promotion.subtitle && (
                        <p className="text-xs sm:text-sm md:text-base text-white/60 mb-3 sm:mb-4 italic line-clamp-2">
                            {promotion.subtitle}
                        </p>
                    )}

                    {/* Discount Highlight */}
                    <div className="mb-3 sm:mb-4 p-3 sm:p-4 bg-[#FF00FF]/10 border border-[#FF00FF]/30 rounded-lg">
                        <p className="text-white/60 text-xs sm:text-sm mb-1">Descuento</p>
                        <p className="text-2xl sm:text-3xl font-bold text-[#FF00FF]" style={{ fontFamily: 'Orbitron' }}>
                            {discountText}
                        </p>
                    </div>

                    {/* Info Pills */}
                    <div className="flex flex-wrap gap-1 sm:gap-2 mb-4 sm:mb-6">
                        {promotion.applicable_to === 'products' && (
                            <span className="bg-blue-500/20 text-blue-400 px-2 sm:px-3 py-1 rounded text-xs whitespace-nowrap">
                                En Productos
                            </span>
                        )}
                        {promotion.applicable_to === 'services' && (
                            <span className="bg-purple-500/20 text-purple-400 px-2 sm:px-3 py-1 rounded text-xs whitespace-nowrap">
                                En Servicios
                            </span>
                        )}
                        {promotion.applicable_to === 'all' && (
                            <span className="bg-[#FF00FF]/20 text-[#FF00FF] px-2 sm:px-3 py-1 rounded text-xs whitespace-nowrap">
                                En Todo
                            </span>
                        )}
                        <span className="bg-white/10 text-white/60 px-2 sm:px-3 py-1 rounded text-xs whitespace-nowrap">
                            Vlida hasta {new Date(promotion.end_date).toLocaleDateString('es-ES', { month: 'short', day: 'numeric' })}
                        </span>
                    </div>

                    {/* Buttons */}
                    <div className="flex flex-col sm:flex-row gap-2 sm:gap-3">
                        <Button
                            onClick={handleViewDetails}
                            className="flex-1 bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white font-bold text-sm sm:text-base h-9 sm:h-10"
                        >
                            Ver Detalles
                        </Button>
                        <Button
                            onClick={handleClose}
                            variant="outline"
                            className="flex-1 border-white/20 text-white hover:bg-white/10 text-sm sm:text-base h-9 sm:h-10"
                        >
                            Cerrar
                        </Button>
                    </div>
                </div>

                {/* Decorative corners */}
                <div className="absolute top-0 left-0 w-3 h-3 sm:w-4 sm:h-4 border-l-2 border-t-2 border-[#FF00FF]/50"></div>
                <div className="absolute top-0 right-0 w-3 h-3 sm:w-4 sm:h-4 border-r-2 border-t-2 border-[#FF00FF]/50"></div>
                <div className="absolute bottom-0 left-0 w-3 h-3 sm:w-4 sm:h-4 border-l-2 border-b-2 border-[#FF00FF]/50"></div>
                <div className="absolute bottom-0 right-0 w-3 h-3 sm:w-4 sm:h-4 border-r-2 border-b-2 border-[#FF00FF]/50"></div>
            </div>
        </div>
    );
}
