import { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ArrowLeft, Clock, Tag, Zap } from 'lucide-react';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { toast } from 'sonner';

const API = `${process.env.REACT_APP_BACKEND_URL}/api`;

export default function PromoDetails() {
    const { promoId } = useParams();
    const navigate = useNavigate();
    const [promotion, setPromotion] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchPromo = async () => {
            try {
                const res = await axios.get(`${API}/promotions/${promoId}`);
                setPromotion(res.data);
                // Scroll al top
                window.scrollTo(0, 0);
            } catch (err) {
                console.error('Error:', err);
                toast.error('Promocin no encontrada');
                navigate('/');
            } finally {
                setLoading(false);
            }
        };
        fetchPromo();
    }, [promoId, navigate]);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-[#030303]">
                <div className="loading-spinner" />
            </div>
        );
    }

    if (!promotion) {
        return (
            <div className="min-h-screen bg-[#030303] pt-24 px-4">
                <Button onClick={() => navigate('/')} variant="ghost" className="text-white">
                    <ArrowLeft className="w-4 h-4 mr-2" />
                    Volver
                </Button>
                <div className="text-white text-center mt-12">Promocin no encontrada</div>
            </div>
        );
    }

    const now = new Date();
    const startDate = new Date(promotion.start_date);
    const endDate = new Date(promotion.end_date);
    const isActive = now >= startDate && now <= endDate && promotion.is_active;

    const discountText = promotion.discount_percentage > 0 
        ? `${promotion.discount_percentage}% de descuento`
        : promotion.discount_fixed > 0
        ? `$${promotion.discount_fixed.toFixed(2)} de descuento`
        : 'Promocin especial';

    return (
        <div className="min-h-screen bg-[#030303] pt-24 pb-12">
            <div className="max-w-4xl mx-auto px-4">
                {/* Header */}
                <Button 
                    onClick={() => navigate('/')} 
                    variant="ghost" 
                    className="text-white mb-8 hover:bg-white/10"
                >
                    <ArrowLeft className="w-4 h-4 mr-2" />
                    Volver
                </Button>

                <Card className="bg-black/50 border-white/10 mb-8">
                    <CardHeader className="border-b border-white/10 pb-0">
                        {/* Image - if available */}
                        {promotion.image_url && (
                            <div className="mb-6 rounded-lg overflow-hidden border border-[#FF00FF]/30 -mx-6 -mt-6">
                                <img 
                                    src={promotion.image_url} 
                                    alt={promotion.name}
                                    className="w-full h-64 md:h-80 object-cover"
                                />
                            </div>
                        )}

                        <div className="flex items-start justify-between gap-4">
                            <div>
                                <CardTitle className="text-white text-3xl md:text-4xl mb-2" style={{ fontFamily: 'Orbitron' }}>
                                    {promotion.name}
                                </CardTitle>
                                
                                {/* Subtitle - if available */}
                                {promotion.subtitle && (
                                    <p className="text-white/60 text-base md:text-lg mb-4 italic">
                                        {promotion.subtitle}
                                    </p>
                                )}

                                <div className="flex flex-wrap gap-3">
                                    {isActive && (
                                        <span className="inline-flex items-center gap-2 bg-green-500/20 text-green-400 px-3 py-1 rounded">
                                            <Zap className="w-4 h-4" />
                                            Promocin Activa
                                        </span>
                                    )}
                                    {promotion.applicable_to === 'products' && (
                                        <span className="bg-blue-500/20 text-blue-400 px-3 py-1 rounded text-sm">
                                            En Productos
                                        </span>
                                    )}
                                    {promotion.applicable_to === 'services' && (
                                        <span className="bg-purple-500/20 text-purple-400 px-3 py-1 rounded text-sm">
                                            En Servicios
                                        </span>
                                    )}
                                    {promotion.applicable_to === 'all' && (
                                        <span className="bg-[#FF00FF]/20 text-[#FF00FF] px-3 py-1 rounded text-sm">
                                            En Todo
                                        </span>
                                    )}
                                </div>
                            </div>
                        </div>
                    </CardHeader>
                    <CardContent className="pt-8">
                        {/* Discount Display */}
                        <div className="mb-8 p-6 bg-gradient-to-r from-[#FF00FF]/10 to-[#00FFFF]/10 border border-white/10 rounded-lg">
                            <p className="text-white/60 text-sm mb-2">Tu descuento</p>
                            <p className="text-white text-3xl font-bold" style={{ fontFamily: 'Orbitron' }}>
                                {discountText}
                            </p>
                        </div>

                        {/* Description */}
                        <div className="mb-8">
                            <h3 className="text-white/70 text-sm uppercase mb-3">Descripcin</h3>
                            <p className="text-white/80 text-lg leading-relaxed">
                                {promotion.description}
                            </p>
                        </div>

                        {/* Details Grid */}
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
                            {/* Valid Period */}
                            <Card className="bg-black/50 border-white/10">
                                <CardHeader className="pb-3">
                                    <div className="flex items-center gap-2">
                                        <Clock className="w-5 h-5 text-[#00FFFF]" />
                                        <h4 className="text-white text-sm uppercase">Perodo de Validez</h4>
                                    </div>
                                </CardHeader>
                                <CardContent>
                                    <div className="space-y-2">
                                        <p className="text-white/60 text-sm">
                                            Desde: <span className="text-white">{new Date(promotion.start_date).toLocaleDateString('es-ES', { 
                                                year: 'numeric', 
                                                month: 'long', 
                                                day: 'numeric',
                                                hour: '2-digit',
                                                minute: '2-digit'
                                            })}</span>
                                        </p>
                                        <p className="text-white/60 text-sm">
                                            Hasta: <span className="text-white">{new Date(promotion.end_date).toLocaleDateString('es-ES', { 
                                                year: 'numeric', 
                                                month: 'long', 
                                                day: 'numeric',
                                                hour: '2-digit',
                                                minute: '2-digit'
                                            })}</span>
                                        </p>
                                    </div>
                                </CardContent>
                            </Card>

                            {/* Discount Details */}
                            <Card className="bg-black/50 border-white/10">
                                <CardHeader className="pb-3">
                                    <div className="flex items-center gap-2">
                                        <Tag className="w-5 h-5 text-[#FF00FF]" />
                                        <h4 className="text-white text-sm uppercase">Descuentos</h4>
                                    </div>
                                </CardHeader>
                                <CardContent>
                                    <div className="space-y-2">
                                        {promotion.discount_percentage > 0 && (
                                            <p className="text-white/60 text-sm">
                                                Descuento %: <span className="text-[#00FFFF] font-bold">{promotion.discount_percentage}%</span>
                                            </p>
                                        )}
                                        {promotion.discount_fixed > 0 && (
                                            <p className="text-white/60 text-sm">
                                                Descuento $: <span className="text-[#FF00FF] font-bold">${promotion.discount_fixed.toFixed(2)}</span>
                                            </p>
                                        )}
                                        {promotion.discount_percentage === 0 && promotion.discount_fixed === 0 && (
                                            <p className="text-white/60 text-sm">Sin descuento especificado</p>
                                        )}
                                    </div>
                                </CardContent>
                            </Card>
                        </div>

                        {/* Conditions */}
                        <Card className="bg-black/50 border-white/10 mb-8">
                            <CardHeader className="pb-3">
                                <h4 className="text-white text-sm uppercase">Condiciones</h4>
                            </CardHeader>
                            <CardContent>
                                <ul className="space-y-2 text-white/60">
                                    <li className="flex items-start gap-2">
                                        <span className="text-[#00FFFF] mt-1"></span>
                                        <span>Vlida desde {new Date(promotion.start_date).toLocaleDateString('es-ES')}</span>
                                    </li>
                                    <li className="flex items-start gap-2">
                                        <span className="text-[#00FFFF] mt-1"></span>
                                        <span>Vlida hasta {new Date(promotion.end_date).toLocaleDateString('es-ES')}</span>
                                    </li>
                                    <li className="flex items-start gap-2">
                                        <span className="text-[#00FFFF] mt-1"></span>
                                        <span>Aplica en {promotion.applicable_to === 'all' ? 'todos los productos y servicios' : promotion.applicable_to === 'products' ? 'productos' : 'servicios'}</span>
                                    </li>
                                </ul>
                            </CardContent>
                        </Card>

                        {/* CTA Button */}
                        {isActive && (
                            <Button 
                                onClick={() => navigate('/')}
                                className="w-full bg-[#FF00FF] hover:bg-[#FF00FF]/90 text-white font-bold text-lg h-12"
                            >
                                Ir a Comprar Ahora
                            </Button>
                        )}
                        </CardContent>
                    </Card>
            </div>
        </div>
    );
}
